import {DOM, ENodeType, EUnknownNodeType} from "lib/commons/xml/dom";
import {IJmlNode, IJmlObj, IJmlSet, JML} from "lib/commons/xml/jml";
import {XA} from "lib/commons/xml/xAddr";
import {XmlDeleteMsg, XmlInsertMsg, XmlStrMsg} from "lib/edit/ot/xmlHouse";
import {CARD, ECard, EPastePos, Schema, SkContext, SkNode} from "lib/edit/schema/schema";
import {EAnnotLevel, EDirectiveType, EFuzzyType, ISkStructDef, SkAnnotAttrMissing, SkAnnotAttrUnknown, SkAnnotEltUnknown, SkAnnotMissing, SkAnnotTextForbidden, SkAnnotWrongValue} from "lib/edit/schema/schemaAnnots";
import {ISkPatternResolver} from "lib/edit/schema/schemaBuilder";
import {SkMatcherAnyAttr, SkMatcherAnyElt, SkMatcherAttr, SkMatcherComment, SkMatcherDoc, SkMatcherNode, SkMatcherText} from "lib/edit/schema/schemaMatchers";
import {ISkMetaNode, SKMETALIB} from "lib/edit/schema/schemaMeta";
import {IConverterProvider} from "lib/edit/import/convert";
import {helpId} from "back/help/helpApi";

type ISkRuleFilter = (rule: SkRule) => boolean

/** SkRule rprsentant un node ou un attribut du DOM, ie pas une directive. */
export type ISkRuleObj = SkRuleNode | SkRuleAttr | SkRuleComment

/**
 * Rgle d'analyse et de traitements d'un noeud dom en fonction d'un Schma.
 */
export abstract class SkRule extends Serializable implements ISkStructDef {
	static readonly registry = new FactoryRegistry<SkRule>("SkRule");

	/**
	 * Nom de cette structure  titre informatif (log, debug...),
	 * n'est pas un identifiant unique fiable (cf uniqueName).
	 */
	structName: string;
	structLabel?: string;
	structType: ENodeType | EFuzzyType | EDirectiveType;

	/**
	 * Mta-modle dfinis pour les lments (SkRuleElt), les attributs (SkRuleAttr) et les comments (SkRuleComment).
	 * Les noeuds textes n'ont pas de mta-modles, d aux pbs de fragmentation non signifiante des noeuds textes;
	 * la mta-modlisation des noeuds textes est porte par l'lment container.
	 */
	skMeta: ISkMetaNode;

	skFamily?:
		/** Familles des balises de blocks. */
		'sub-level' | 'content' | 'property' | 'property/title' | 'property/url'
		/** Familles des balises inline fondes sur la smantique HTML. */
		| 'a' | 'b' | 'cite' | 'code' | 'em' | 'i' | 'kbd' | 'q' | 'samp' | 'small' | 'strong' | 'sub' | 'sup';

	skMalusCoef?: number;

	initSkMeta(skMeta: ISkMetaNode): this {
		this.skMeta = skMeta;
		return this;
	}

	abstract structMatch(nodeType: ENodeType | EUnknownNodeType, nodeName?: string): boolean;

	/**
	 *
	 * @param guardStack usage interne, controle de rcursivit.
	 */
	abstract createContent(childrenToAppend?: IJmlSet, attsToAppend?: IJmlObj, guardStack?: SkRuleElt[]): void | string;

	/**
	 * Identifiant unique de la directive : signifie que cette Rule peut tre rutilise,
	 * et donc optimisation par fusion avec un contexte parent impossible.
	 */
	uniqueName?: string;

	/** Surcharge locale  cette Rule de la rgle pour les elts inconnus. */
	unknownEltRule?: SkRule;
	/** Surcharge locale  cette Rule de la rgle pour les noeuds textes interdits. */
	forbiddenTextRule?: SkRule;
	/** Surcharge locale  cette Rule de la rgle pour les attributs inconnus. */
	unknownAttrRule?: SkRule;
	/** Surcharge locale  cette Rule de la rgle pour les comments. */
	commentRule?: SkRule;

	/**
	 * Cardinalit de cette rule.
	 */
	get card(): ECard {return '1'}

	set card(card: ECard) {}

	/**
	 * Recherche le 1er {SkRuleNode} matchant node pour initier une association avec ce noeud.
	 * @param node
	 * @return Retourne null si cette rule ne contient aucun {SkRuleNode} matchant node.
	 */
	findRuleNodeFor(node: Node | IJmlNode): SkRuleNode {return null}

	/**
	 * Recherche le 1er {SkRuleAttr} matchant attr pour initier une association avec cet attribut.
	 * @param attr
	 * @return Retourne null si cette rule ne contient aucun {SkRuleAttr} matchant node.
	 */
	findRuleAttrFor(attr: string): SkRuleAttr {return null}

	/**
	 * Recherche une Rule parmi l'arbre des Rules, en traversant les directives.
	 */
	findRule(filter: ISkRuleFilter, includeDirectives?: boolean): SkRule | null {return filter(this) ? this : null}

	/**
	 * Recherche une Rule parmi l'arbre des Rules, en traversant les directives, sans jamais les inclure.
	 */
	findRules(filter: ISkRuleFilter, result?: ISkRuleObj[]): ISkRuleObj[] | undefined {
		if (filter(this)) (result || (result = [])).push(this as any);
		return result;
	}

	/**
	 * Parcours les rules en peuplant en parallle une stack des directives anctres.
	 * Si filter retourne true, le parcours est interrompu.
	 */
	scanRules(filter: ISkRuleFilter, directives: SkRuleDirective[], includeDirectives?: boolean): SkRule | null {return filter(this) ? this : null}

	/**
	 * Recherche ou construit un SkRule dfinissant ce qui peut tre insr aprs from.
	 * this doit tre une Rule racine du contenu du contexte parent (rule autonome intgrant sa cardinalit).
	 *
	 * @param from doit exister dans le content de this
	 * @return null si aucune insertion possible.
	 */
	buildRuleAfter(from: ISkRuleObj, ctxCard: ECard = '1'): SkRule {
		const realCard = CARD.combineCard(this.card, ctxCard);
		return CARD.isRepeatable(realCard) ? this : SkRuleEmpty.SINGLETON;
	}

	/** Retourne la card d'une subRule ou null si la rule n'est ni this ni dans la descendance des directives. */
	getRealCardSubRule(rule: SkRule): ECard | undefined {
		return this === rule ? this.card : undefined;
	}

	isEltRemovable(elt: Element): boolean {
		if (this.card === '+') {
			//removable si on n'est pas le dernier.
			let sib = elt.previousElementSibling;
			if (sib && this.structMatch(ENodeType.element, sib.nodeName)) return true;
			sib = elt.nextElementSibling;
			if (sib && this.structMatch(ENodeType.element, sib.nodeName)) return true;
			return false;
		}
		return CARD.isOptionnal(this.card);
	}

	/**
	 * 1re tape du processus de validation. Partant d'un scope de noeuds fils et d'attributs,
	 * recherche les meilleures associations possibles aux rgles. Cette mthode exploite skCtx
	 * pour mmoriser la solution optimale.
	 *
	 * La 2me tape du processus de validation consiste  binder les lments fils et excuter les actions
	 * suceptibles de l'tre ds leur binding (cf SkRuleNode#execActionsOnBinding() et SkRuleAttr#execActionsOnBinding())
	 * Cette 2me tape est ralise par SkContext#bindRulesAndExecute().
	 *
	 * Pour la 3me et dernire tape, cf Rule.execActions().
	 *
	 * @param skCtx Contexte d'excution
	 * @param matchMode Mode de matching, cf SkMatchMode.
	 * @param contextCard Cardinalit du contexte parent  combiner avec sa propre card: CARD.combineCard(contextCard, this.card)
	 */
	findBestMatching(skCtx: SkContext, matchMode = ESkMatchMode.all, contextCard: ECard = '1') {}

	/**
	 * Utilis par les directives (choice, group, unorderedGroup) pour valuer si une sous-rgle correspond au contexte courant
	 * (ie, attributs restant  binder ou skCtx.currentChild).
	 *
	 * @param skCtx
	 * @return {boolean}
	 */
	isMatching(skCtx: SkContext): boolean {return false}

	/**
	 * 3me tape du processus de validation (voir #findBestMatching() pour les tapes 1 et 2).
	 *
	 * Excute les actions relative  cette rule.
	 * Attention, les directives doivent r-appeler ses subRules dans le meme ordre que findBestMatching() pour que
	 * le principe de pile mmoire soit oprationnel.
	 *
	 * @param skCtx Contexte d'execution.
	 * @param matchMode Mode de matching, cf SkMatchMode.
	 * @param contextCard Cardinalit du contexte parent  combiner avec sa propre card: CARD.combineCard(contextCard, this.card)
	 */
	execActions(skCtx: SkContext, contextCard: ECard = '1') {}

	xAppendInsertables(skCtx: SkContext, listNodes: ISkStructDef[], listAttrs?: ISkStructDef[]) {}

	getUnknownNodeRule(skNode: SkNode, node: Node | IJmlNode): SkRuleNode {
		switch (node instanceof Node ? node.nodeType : JML.jmlNode2nodeType(node)) {
		case ENodeType.element:
			if (this.unknownEltRule) {
				const rule = this.unknownEltRule.findRuleNodeFor(node);
				if (rule) return rule;
			}
			return skNode.schemaDom.schema.unknownEltRule.findRuleNodeFor(node);
		case ENodeType.text:
			if (DOM.WHITESPACES.test(node instanceof Node ? node.nodeValue : node as string)) {
				//Au cas o la normalisation du flux XML n'a pas permis de suppr les noeuds WS d  la prsence d'un xml:space=preserve
				// ex: metas dans les balises inline d'un para, le flux XML ayant t indent par un outil non respectueux du xml:space=preserve.
				return SkRuleTextWs.DEFAULT;
			}
			if (this.forbiddenTextRule) {
				const rule = this.forbiddenTextRule.findRuleNodeFor(node);
				if (rule) return rule;
			}
			return skNode.schemaDom.schema.forbiddenTextRule.findRuleNodeFor(node);
		case ENodeType.comment:
			if (this.commentRule) {
				const rule = this.commentRule.findRuleNodeFor(node);
				if (rule) return rule;
			}
			return skNode.schemaDom.schema.commentRule.findRuleNodeFor(node);
		}
	}

	getUnknownAttRule(skNode: SkNode, attr: string): SkRuleAttr {
		if (this.unknownAttrRule) {
			const rule = this.unknownAttrRule.findRuleAttrFor(attr);
			if (rule) return rule;
		}
		return skNode.schemaDom.schema.unknownAttrRule.findRuleAttrFor(attr);
	}

	getRealCardSubNode(nodeType: ENodeType, nodeName: string): ECard | undefined {
		return this.structMatch(nodeType, nodeName) ? this.card : undefined;
	}

	checkSchemaOrder(eltName1: string, eltName2: string): boolean | undefined {return undefined}

	/** Linkaged des rules qui rfrencent d'autres rules par leur name. */
	linkRules(map: ISkPatternResolver): SkRule {
		return this;
	}


	writeTo(o: any, mode: ESerialMode) {
		if (this.uniqueName != null) o.un = this.uniqueName;
	}

	readFrom(o: any, mode: ESerialMode): this {
		if (o.un != null) this.uniqueName = o.un;
		return this;
	}


	/**
	 * Id unique de cette Rule pour la srialisation et la dsrialisation du schma.
	 * Les patterns singletons n'ont jamais d'id (undefined).
	 * A la srialisation, si l'id === undefined, la Rule sera srialise inline.
	 * ATTENTION : calcul que lors de la srialisation du schma contenant cette rule.
	 * ==> API srialisation  ne pas utiliser hors srialisation du schma complet
	 */
	id: number | undefined;

	/** On ne peut pas utiliser ici Serializable.toString() car this.id non calcul => cycle infini. */
	toString(): string {return this.structName}

	/** Indexation des patterns d'un schma. */
	abstract indexPatterns(rulesIndex: SkRule[]): void;

	/** Srialisation d'un Pattern en inline ou par son id. */
	static serialRefRule(r: SkRule, mode: ESerialMode): any {
		if (r.id !== undefined) return r.id;
		return Serializable.serialSerializable(r, mode);
	}

	/** Dsrialisation d'un Pattern en inline ou par son id. */
	static deserialRefRule(r: any, mode: ESerialMode): SkRule | number {
		if (typeof r === 'number') return r;
		return Serializable.deserialSerializable(r, mode, SkRule.registry);
	}

}

/**
 * Mode d'excution pour SkRule#findBestMatching(skCtx, matchMode, contextCard) et SkRule#execActions(skCtx, contextCard).
 */
enum ESkMatchMode {
	/** Match tous les nodes et attributs, y compris ceux inconnus en les associant au SkRule unkonwn. */
	all,

	/** Match le(s) premier(s) noeuds jusqu'au 1er qui ne match plus et match les attributs reconnus. */
	ordered
}

/**
 * Rgle reprsentant un noeud concret (Document, Element, Text).
 */
export abstract class SkRuleNode extends SkRule {

	/**
	 * Rgle  appliquer pour le contenu de ce noeud constitu de ses attributs et de ses noeuds fils.
	 * Sauf cas trivial contenant un seul attr ou elt fils, cette rgle est gnralement une directive.
	 */
	contentRule: SkRule;

	/** Ref temporaire au contenu avant linkage pouvant pointer la contentRule par son name. */
	tmpCtRule: string | number | SkRule;

	/**
	 * Matcher de cette rgle avec le noeud auquel il pourrait tre associ.
	 */
	matcher: SkMatcherNode;

	initContentRule(contentRule: SkRule = SkRuleEmpty.SINGLETON): this {
		this.contentRule = contentRule;
		this.tmpCtRule = undefined;
		return this;
	}

	initContentName(contentRule: string | SkRule): this {
		this.tmpCtRule = contentRule;
		return this;
	}

	linkRules(map: ISkPatternResolver): SkRule {
		if (this.tmpCtRule === null) return this; //linkRules() dj appel (pb rcursivit).
		if (this.tmpCtRule !== undefined) this.contentRule = map.resolveRule(this.tmpCtRule);
		this.tmpCtRule = null; //marque le linkRule done (pb rcursivit).
		if (this.contentRule) this.contentRule = this.contentRule.linkRules(map);
		return this;
	}

	// @ts-ignore
	get structName() {return this.matcher.techName}

	createContent(childrenToAppend?: IJmlSet, attsToAppend?: IJmlObj, guardStack?: SkRuleElt[]): void | string {}

	/**
	 * Suite  l'association de cette rgle  un noeud, excute les actions relative  cette rule.
	 * Attention, la pile mmoire du contexte ne peut tre exploite ici.
	 * Mthode utilis par les rules de type unknown qui ne sont pas appeles dans l'tape SkRule.execActions().
	 * @param {SkContext} skCtx Contexte d'execution
	 * @param {Node} node Noeud sur lequel cette rgle est associe.
	 */
	execActionsOnBinding(skCtx: SkContext, node: Node) {
	}

	findRuleNodeFor(node: Node | IJmlNode): SkRuleNode {
		if (node instanceof Node) return this.matcher.matchNode(node.nodeType, node.nodeName) && this.deepMatch(node) ? this : null;
		return this.matcher.matchNode(JML.jmlNode2nodeType(node), JML.jmlNode2name(node)) && this.deepMatch(node) ? this : null;
	}

	isMatching(skCtx: SkContext) {
		const node = skCtx.currentChild;
		return node ? this.matcher.matchNode(node.nodeType, node.nodeName) : false;
	}

	deepMatch(node: Node | IJmlNode): boolean {return true}

	indexPatterns(rulesIndex: SkRule[]) {
		if (Object.isFrozen(this)) return;
		if (this.id === undefined) {
			this.id = rulesIndex.length;
			rulesIndex.push(this);
			if (this.contentRule) this.contentRule.indexPatterns(rulesIndex);
		}
	}
}

/**
 * Rgles non concrtes (choice, group, unorderedGroup, empty) spcifiant les agencements possibles du contenu
 * d'un {SkRuleNode}.
 */
export abstract class SkRuleDirective extends SkRule {

	subRules: SkRule[];
	subCards: ECard[];

	/** Tableau temporaire avant linkage pouvant pointer les subRules par leur name. */
	tmpSubRules: Array<string | number | SkRule>;

	/** Name de la directive construit automatiquement. */
	protected _builtName: string;

	protected _card: ECard = '1';

	initName(name: string): this {
		this.uniqueName = name;
		return this;
	}

	initCard(card: ECard): this {
		this._card = card;
		return this;
	}

	initSubRules(subRules: SkRule[]): this {
		this.subRules = subRules;
		return this;
	}

	initSubNames(subNames: Array<string | SkRule>): this {
		this.tmpSubRules = subNames;
		return this;
	}

	addSubName(subName: string | SkRule): this {
		if (!this.tmpSubRules) this.tmpSubRules = [subName];
		else this.tmpSubRules.push(subName);
		return this;
	}

	structMatch(nodeType: ENodeType, nodeName?: string): boolean {
		for (let i = 0, s = this.subRules.length; i < s; i++) if (this.subRules[i].structMatch(nodeType, nodeName)) return true;
		return false;
	}

	linkRules(map: ISkPatternResolver): SkRule {
		if (this.tmpSubRules) {
			this.subRules = this.tmpSubRules.map(r => map.resolveRule(r));
			this.tmpSubRules = undefined;
			this.subRules.forEach((r, i) => {
				let rule = r.linkRules(map);
				while (rule instanceof SkRuleDirective && !rule.structLabel && rule.subRules.length === 1 && !rule.uniqueName && !Object.isFrozen(rule)) {
					this.setOverSubCard(i, this.overSubCard(i, rule.card));
					rule = rule.subRules[0];
				}
				this.subRules[i] = rule;
			});
			if (this.subRules.length === 1 && !this.structLabel) {
				const rule = this.subRules[0];
				if (!rule.uniqueName && !Object.isFrozen(rule)) {
					//Optimisation possible : suppr de cette directive inutile par fusion de la cardinalit.
					rule.card = CARD.combineCard(this.card, rule.card);
					rule.uniqueName = this.uniqueName;
					return rule;
				}
			}
		}
		return this;
	}

	get card() {return this._card}

	set card(card: ECard) {
		this._card = card
	}


	findRuleNodeFor(node: Node | IJmlNode): SkRuleNode {
		for (let i = 0, s = this.subRules.length; i < s; i++) {
			const found = this.subRules[i].findRuleNodeFor(node);
			if (found) return found;
		}
		return null;
	}

	findRuleAttrFor(attr: string): SkRuleAttr {
		for (let i = 0, s = this.subRules.length; i < s; i++) {
			const found = this.subRules[i].findRuleAttrFor(attr);
			if (found) return found;
		}
		return null;
	}

	findRule(filter: ISkRuleFilter, includeDirectives?: boolean): SkRule | null {
		if (includeDirectives && filter(this)) return this;
		for (let i = 0, s = this.subRules.length; i < s; i++) {
			const result = this.subRules[i].findRule(filter, includeDirectives);
			if (result != null) return result;
		}
		return null;
	}

	findRules(filter: ISkRuleFilter, result?: ISkRuleObj[]): ISkRuleObj[] {
		//if (filter(this)) result.push(this); intrt ?
		for (let i = 0, s = this.subRules.length; i < s; i++) {
			result = this.subRules[i].findRules(filter, result);
		}
		return result;
	}

	scanRules(filter: ISkRuleFilter, directives: SkRuleDirective[], includeDirectives?: boolean): SkRule | null {
		if (includeDirectives && filter(this)) return this;
		directives.push(this);
		for (let i = 0, s = this.subRules.length; i < s; i++) {
			const result = this.subRules[i].scanRules(filter, directives, includeDirectives);
			if (result != null) return result;
		}
		directives.pop();
		return null;
	}

	getRealCardSubRule(rule: SkRule): ECard | undefined {
		if (this === rule) return this.card;
		for (let i = 0, s = this.subRules.length; i < s; i++) {
			const card = this.subRules[i].getRealCardSubRule(rule);
			if (card != null) return CARD.combineCard(this.overSubCard(i, this.card), card);
		}
		return undefined;
	}

	getRealCardSubNode(nodeType: ENodeType, nodeName: string): ECard | undefined {
		for (let i = 0, s = this.subRules.length; i < s; i++) {
			const subR = this.subRules[i];
			if (subR.structMatch(nodeType, nodeName)) {
				const subCard = subR.getRealCardSubNode(nodeType, nodeName);
				return CARD.combineCard(this.card, this.overSubCard(i, subCard));
			}
		}
		return undefined;
	}

	isEltRemovable(elt: Element): boolean {
		switch (this.card) {
		case '?':
		case '*':
			return true;
		case '+':
			return super.isEltRemovable(elt);
		default : // '1'
			//on descend
			for (let i = 0, s = this.subRules.length; i < s; i++) {
				const subR = this.subRules[i];
				if (subR.structMatch(ENodeType.element, elt.nodeName)) {
					return subR.isEltRemovable(elt);
				}
			}
			console.trace("Should never occur"); //Le contexte amont aurait du dtecter que elt ne matchait pas cette rule.
			return false;
		}
	}

	checkSchemaOrder(eltName1: string, eltName2: string): boolean | undefined {
		for (let i = 0, s = this.subRules.length; i < s; i++) {
			const subR = this.subRules[i];
			//Si les 2 names sont dans le mme fils on interroge le fils
			//Ds qu'un des noms match une rule fils et pas l'autre, ordre impossible  connaitre.
			const inSub1 = subR.structMatch(ENodeType.element, eltName1);
			if (subR.structMatch(ENodeType.element, eltName2)) {
				if (inSub1) return subR.checkSchemaOrder(eltName1, eltName2);
				return undefined;
			} else {
				if (inSub1) return undefined;
			}
		}
		return undefined;
	}

	protected overSubCard(i: number, contextCard: ECard): ECard {
		if (!this.subCards) return contextCard;
		const card = this.subCards[i];
		if (!card) return contextCard;
		return CARD.combineCard(contextCard, card);
	}

	protected setOverSubCard(i: number, card: ECard) {
		if (card == '1') return;
		if (!this.subCards) this.subCards = [];
		this.subCards[i] = card;
	}


	indexPatterns(rulesIndex: SkRule[]) {
		if (Object.isFrozen(this)) return;
		if (this.id === undefined) {
			this.id = rulesIndex.length;
			rulesIndex.push(this);
			this.subRules.forEach(r => {
				r.indexPatterns(rulesIndex)
			});
		}
	}

	writeTo(o: any, mode: ESerialMode) {
		super.writeTo(o, mode);
		o.rules = this.subRules.map(r => SkRule.serialRefRule(r, mode));
		if (this.subCards != null) o.cards = Serializable.serialArrayPrimitives(this.subCards, mode);
		o.card = this._card;
	}

	readFrom(o: any, mode: ESerialMode): this {
		super.readFrom(o, mode);
		if (o.un != null) this.uniqueName = o.un;
		this.tmpSubRules = o.rules.map((m: any) => SkRule.deserialRefRule(m, mode));
		if (o.cards != null) this.subCards = o.cards;
		this._card = o.card;
		return this;
	}
}

/**
 * Rgle associe  un noeud de type Document.
 */
export class SkRuleDoc extends SkRuleNode {
	static readonly type = "/";

	init(contentRule: SkRule, skMeta = SKMETALIB.getMetaNode('/'), matcher: SkMatcherNode = SkMatcherDoc.SINGLETON): this {
		this.contentRule = contentRule;
		this.matcher = matcher;
		this.skMeta = skMeta;
		return this;
	}

	// @ts-ignore
	get structType() {return ENodeType.document}

	structMatch(nodeType: ENodeType, nodeName?: string): boolean {return nodeType === ENodeType.document}

	//SkRuleDoc n'est jamais une rgle descendante d'un SkRuleNode.contentRule
	findBestMatching(skCtx: SkContext, matchMode: ESkMatchMode, contextCard: ECard) {
		throw Error("Should never be called!");
	}

	isMatching(skCtx: SkContext): boolean {
		throw Error("Should never be called!");
	}

	execActions(skCtx: SkContext, contextCard: ECard) {
		throw Error("Should never be called!");
	}

	createContent(childrenToAppend?: IJmlSet, attsToAppend?: IJmlObj): void | string {
		this.contentRule.createContent(childrenToAppend, attsToAppend);
	}

	writeTo(o: any, mode: ESerialMode) {
		super.writeTo(o, mode);
		if (this.matcher !== SkMatcherDoc.SINGLETON) o.matcher = Serializable.serialSerializable(this.matcher, mode);
		o.ctRule = SkRule.serialRefRule(this.contentRule, mode);
	}

	readFrom(o: any, mode: ESerialMode): this {
		super.readFrom(o, mode);
		this.matcher = o.matcher ? Serializable.deserialSerializable(this.matcher, mode, SkMatcherNode.registry) : SkMatcherDoc.SINGLETON;
		this.tmpCtRule = SkRule.deserialRefRule(o.ctRule, mode);
		return this;
	}
}

SkRule.registry.register(SkRuleDoc);

/**
 * Rgle correspondant  un lment.
 */
export class SkRuleElt extends SkRuleNode {
	static readonly type = "*";

	/** Ensemble des convertisseurs susceptibles de produire un tel lment racine. */
	public convertFrom: IConverterProvider;

	/** Entre d'aide. */
	helpId?: helpId

	protected _card: ECard;

	/** Bloque l'autoComplete mme si cet lment est required. */
	noAutoComplete: boolean;

	init(matcher: SkMatcherNode, card: ECard = '1', contentRule?: SkRule): this {
		this.matcher = matcher;
		this._card = card;
		this.contentRule = contentRule || SkRuleEmpty.SINGLETON;
		return this;
	}

	get card(): ECard {return this._card}

	set card(card: ECard) {
		this._card = card;
	}

	// @ts-ignore
	get structType() {return this.matcher.isFuzzy() ? EFuzzyType.elements : ENodeType.element}

	isMatching(skCtx: SkContext) {
		const node = skCtx.currentChild;
		return node ? this.matcher.matchNode(node.nodeType, node.nodeName) && this.deepMatch(node as Element) : false;
	}

	isMatchNode(node: Node) {
		return this.matcher.matchNode(node.nodeType, node.nodeName) && this.deepMatch(node as Element);
	}

	isSameRoot(other: SkRuleElt): boolean {
		return this.matcher.startName === other.matcher.startName;
	}

	deepMatch(node: Node | IJmlNode): boolean {
		return this.skMeta ? this.skMeta.ruleMatch(this, node) : true;
	}

	createContent(childrenToAppend?: IJmlSet, attsToAppend?: IJmlObj, guardStack?: SkRuleElt[]): void | string {
		if (guardStack && this.noAutoComplete) return; //on n'est le root de la demande de construction et on refuse l'autoComplete, on sort.
		if (this.matcher.isFuzzy()) return this.matcher.startName;
		const elt = {'': this.matcher.startName};
		childrenToAppend.push(elt);
		if (CARD.isRequired(this.contentRule.card)) {
			const children: IJmlSet = [];
			if (guardStack) {
				if (guardStack.indexOf(this) >= 0) throw Error(`Infinite cycle in schema (with required cardinality): ${guardStack.join('/')}/${guardStack[0]}`);
				guardStack.push(this);
			} else guardStack = [this];
			this.contentRule.createContent(children, elt, guardStack);
			if (children.length > 0) childrenToAppend.push(children);
			if (guardStack) guardStack.length--;
		}
		//this.skMeta.
	}

	/** Cre un noeud et ses ventuels attributs diffrenciant. */
	createNode(parent: Node): Node {
		const result = (parent.ownerDocument || parent as Document).createElement(this.matcher.startName);
		if (this.skMeta && ('onCreateNode' in this.skMeta)) this.skMeta.onCreateNode(this, result);
		return result;
	}


	structMatch(nodeType: ENodeType, nodeName?: string): boolean {return this.matcher.matchNode(nodeType, nodeName)}

	findBestMatching(skCtx: SkContext, matchMode = ESkMatchMode.all, contextCard: ECard = '1') {
		const repeatable = CARD.isRepeatable(CARD.combineCard(contextCard, this.card));
		let found = 0;
		switch (matchMode) {
		case ESkMatchMode.all: {
			skCtx.forEachChild(ch => {
				if ((found == 0 || repeatable) && this.isMatchNode(ch)) {
					found++;
					skCtx.bindNodeToRule(ch, this);
				} else {
					skCtx.bindNodeToRule(ch, this.getUnknownNodeRule(skCtx.skNode, ch));
				}
			});
			skCtx.forEachAttr(a => {
				skCtx.bindAttrToRule(a, this.getUnknownAttRule(skCtx.skNode, a.nodeName));
			});
			break;
		}
		case ESkMatchMode.ordered:
			skCtx.forEachBindableChild(ch => {
				if ((found == 0 || repeatable) && this.isMatchNode(ch)) {
					found++;
					skCtx.bindNodeToRule(ch, this);
				} else return true; //on stop le parcours des fils.
			});
			break;
		}
		skCtx.pushRuleState(found);
	}

	execActions(skCtx: SkContext, contextCard: ECard = '1') {
		const found: number = skCtx.shiftRuleState();
		const card = CARD.combineCard(contextCard, this.card);
		const opts = skCtx.execOptions;
		if (opts.list1InsertableNodesAt !== undefined) {
			if (found === 0 || CARD.isRepeatable(card)) {
				const min = skCtx.currentOffsetMin;
				const max = skCtx.getOffsetMaxAfterCurrent(found);
				const insPoint1 = opts.list1InsertableNodesAt;
				if (insPoint1 === -1 || (min <= insPoint1 && max >= insPoint1)) {
					this.xAppendInsertables(skCtx, opts.insertableNodes1);
				}
				const insPoint2 = opts.list2InsertableNodesAt;
				if (insPoint2 >= 0 && min <= insPoint2 && max >= insPoint2) {
					this.xAppendInsertables(skCtx, opts.insertableNodes2);
				}
			}
		}

		if (opts.importContent
			&& (opts.importContent instanceof SkRule ? this === opts.importContent : this.findRuleNodeFor(opts.importContent))) {
			if ('pasteOffsetMin' in opts) {
				const minIns = skCtx.currentOffsetMin;
				const maxIns = skCtx.getOffsetMaxAfterCurrent(found);
				const insBefore = opts.pasteOffsetMin >= minIns && opts.pasteOffsetMin <= maxIns;
				const insAfter = opts.pasteOffsetMax >= minIns && opts.pasteOffsetMax <= maxIns;
				if (insBefore || insAfter) {
					//Une insertion est possible
					if (opts.pasteOffsetMin === opts.pasteOffsetMax) {
						//Si importOffsetMin == importOffsetMax, on insre dans un gap, ou dans un contexte virtuel => replace
						opts.pastePos = EPastePos.replace;
					}
					if (CARD.isRepeatable(card)) {
						if (insBefore) {
							opts.pastePos = insAfter ? EPastePos.anywhere : EPastePos.before | EPastePos.replace;
						} else {
							opts.pastePos = EPastePos.replace | EPastePos.after;
						}
					} else if (found === 0) {
						opts.pastePos = insBefore ? EPastePos.before : EPastePos.after; //Si insBefore && insAfter -> EPastePos.before | EPastePos.after ?
					} else {
						opts.pastePos = EPastePos.replace;
					}
				}
			} else {
				if (CARD.isRepeatable(card)) {
					opts.importPos = {insertOffsetMin: skCtx.currentOffsetMin, insertOffsetMax: skCtx.getOffsetMaxAfterCurrent(found), replaceChildren: [skCtx.currentOffset]};
					let n = skCtx.currentChild as Element;
					for (let i = 1; i < found; i++) {
						n = n.nextElementSibling;
						opts.importPos.replaceChildren.push(DOM.computeOffset(n));
					}
				} else if (found > 0) {
					//trouv, on remplace
					opts.importPos = {replaceChildren: [skCtx.currentOffset]};
				} else {
					//pas trouv, on insre
					opts.importPos = {insertOffsetMin: skCtx.currentOffsetMin, insertOffsetMax: skCtx.getOffsetMaxAfterCurrent(found)};
				}
			}
		}
		let addFindOffsetAfter;
		if (opts.findOffsetNodeType && this.structMatch(opts.findOffsetNodeType, opts.findOffsetNodeName)) {
			switch (opts.findOffsetInGroup || (CARD.isRepeatable(card) ? opts.findOffsetIfExistAndCardN : 'reject')) {
			case "reject":
				opts.findOffset = found > 0 ? -1 : skCtx.currentOffsetMax;
				break;
			case "start":
				if (!('findOffset' in opts)) opts.findOffset = skCtx.currentOffset;
				break;
			case "end":
				addFindOffsetAfter = true;
			}
		}
		if (found === 0) {
			if (CARD.isRequired(card)) {
				if (opts.autoComplete && !this.noAutoComplete) {
					const content: IJmlSet = [];
					this.createContent(content, null);
					if (content.length > 0) opts.corrections.push(new XmlInsertMsg().init(XA.append(XA.from(skCtx.node), skCtx.currentOffset), content));
				} else if (opts.genAnnots) {
					skCtx.addAnnot(new SkAnnotMissing().init(SkAnnotMissing.TYPE_eltMissing, skCtx.node, skCtx.currentOffset, this));
				}
			}
		} else {
			for (let i = 0; i < found; i++) skCtx.currentChild = skCtx.currentChild.nextSibling;
		}
		if (addFindOffsetAfter) opts.findOffset = skCtx.currentOffsetMax;
	}

	xAppendInsertables(skCtx: SkContext, listNodes: ISkStructDef[], listAttrs?: ISkStructDef[]) {
		if (listNodes?.indexOf(this) < 0 && this.skMeta.isInsertable(skCtx.skNode, this)) listNodes.push(this);
	}

	writeTo(o: any, mode: ESerialMode) {
		super.writeTo(o, mode);
		o.matcher = Serializable.serialSerializable(this.matcher, mode);
		o.ctRule = SkRule.serialRefRule(this.contentRule, mode);
		o.card = this.card;
	}

	readFrom(o: any, mode: ESerialMode): this {
		super.readFrom(o, mode);
		this.matcher = Serializable.deserialSerializable(o.matcher, mode, SkMatcherNode.registry);
		this.tmpCtRule = SkRule.deserialRefRule(o.ctRule, mode);
		this._card = o.card;
		return this;
	}

}

SkRule.registry.register(SkRuleElt);

export class SkRuleEltUnknown extends SkRuleNode {
	static readonly type = "*?";

	initUnknown(matcher: SkMatcherNode = SkMatcherAnyElt.SINGLETON, skMeta = SKMETALIB.getMetaNode('*')): this {
		this.matcher = matcher;
		this.contentRule = this; //pour la rcursivit de l'affectation des skNodes aux sous-noeuds inconnus.
		this.skMeta = skMeta;
		return this;
	}

	get card(): ECard {return '?'}

	// @ts-ignore
	get structType() {return EFuzzyType.elements}

	structMatch(nodeType: ENodeType, nodeName?: string): boolean {return this.matcher.matchNode(nodeType, nodeName)}

	//isMatching(skCtx){return false}
	//execActions(skCtx, contextCard) {}

	findBestMatching(skCtx: SkContext, matchMode = ESkMatchMode.all, contextCard: ECard = '1') {
		//Rcusivit des lements inconnus
		skCtx.forEachChild(ch => {
			skCtx.bindNodeToRule(ch, this.getUnknownNodeRule(skCtx.skNode, ch));
		});
		skCtx.forEachAttr(a => {
			skCtx.bindAttrToRule(a, this.getUnknownAttRule(skCtx.skNode, a.nodeName));
		});
	}

	execActionsOnBinding(skCtx: SkContext, elt: Element) {
		if (skCtx.skNode.rule instanceof SkRuleEltUnknown) return; //Aucun traitement si on n'est pas l'elt unkonwn root.
		if (skCtx.execOptions.autoCleanup) skCtx.execOptions.corrections.push(new XmlDeleteMsg().init(XA.from(elt), 1));
		else if (skCtx.execOptions.genAnnots) skCtx.addAnnot(new SkAnnotEltUnknown().init(elt));
	}

	linkRules() {return this}

	writeTo(o: any, mode: ESerialMode) {
		if (this === SkRuleEltUnknown.DEFAULT) return;
		super.writeTo(o, mode);
		o.matcher = this.matcher !== SkMatcherAnyElt.SINGLETON ? Serializable.serialSerializable(this.matcher, mode) : null;
		if (this.contentRule !== this) o.ctRule = SkRule.serialRefRule(this.contentRule, mode);
	}

	readFrom(o: any, mode: ESerialMode): this {
		if (o.matcher === undefined) return SkRuleEltUnknown.DEFAULT as this;
		super.readFrom(o, mode);
		this.matcher = o.matcher ? Serializable.deserialSerializable(this.matcher, mode, SkMatcherNode.registry) : SkMatcherAnyElt.SINGLETON;
		if (o.ctRule) this.tmpCtRule = SkRule.deserialRefRule(o.ctRule, mode);
		else this.contentRule = this;
		return this;
	}

	static readonly DEFAULT = new SkRuleEltUnknown();
}

SkRule.registry.register(SkRuleEltUnknown);


/** Mthodes ddies  la validation des strings (contenu des noeuds texte et des attributs). */
export interface ISkRuleStr extends SkRule {

	/** Evalue si cette string est valide (pour choice et Ugroup). */
	matchStr(str: string, contextCard: ECard): boolean;

	//matchStrList(strs: string[], contextCard: OCard): boolean;

	/**
	 * Evalue cette string et publie les annotations d'erreur.
	 * @param anchor Pour les noeuds texte, la validation est ancre sur l'lment parent
	 * car il est interdit d'avoir un texte typ combin avec d'autres lments
	 * (http://relaxng.org/spec-20011203.html#string-sequences). Permet de traiter
	 * l'absence de noeuds textes ou un noued texte "" de faon transparente.
	 */
	validStr(str: string, contextCard: ECard, anchor: Element | Attr, skCtx: SkContext): void;

	//validStrList(strs: string[], contextCard: OCard, anchor: Node | Attr, skCtx: SkContext): void;

	getDefaultValue(): string;

}

export function isSkRuleStr(rule: SkRule): rule is ISkRuleStr {return rule && ('matchStr' in rule)}


/** Rule pour un noeud texte (exploit aussi pour valider le contenu d'un attribut, cf SkRuleAttr.valueCheck). */
export abstract class SkRuleStr extends SkRuleNode implements ISkRuleStr {

	// @ts-ignore
	get structType() {return ENodeType.text}

	structMatch(nodeType: ENodeType, nodeName?: string): boolean {return nodeType === ENodeType.text}

	// @ts-ignore
	get matcher(): SkMatcherNode {return SkMatcherText.SINGLETON}

	/**
	 * Les string successives seront concatns et valides ensuite comme un noeud unique.
	 * On autorise donc toujours this en rule suivante mme si card mono.
	 */
	buildRuleAfter(from: ISkRuleObj, ctxCard: ECard = '1'): SkRule {
		return this;
	}

	createContent(childrenToAppend?: IJmlSet, attsToAppend?: IJmlObj): void | string {
		childrenToAppend.push(this.getDefaultValue());
	}

	getDefaultValue(): string {return ""}

	findBestMatching(skCtx: SkContext, matchMode = ESkMatchMode.all, contextCard = '1') {
		switch (matchMode) {
		case ESkMatchMode.all: {
			skCtx.forEachChild(ch => {
				if (ch.nodeType === ENodeType.text) {
					skCtx.bindNodeToRule(ch, this);
				} else {
					skCtx.bindNodeToRule(ch, this.getUnknownNodeRule(skCtx.skNode, ch));
				}
			});
			skCtx.forEachAttr(a => {
				skCtx.bindAttrToRule(a, this.getUnknownAttRule(skCtx.skNode, a.nodeName));
			});
			break;
		}
		case ESkMatchMode.ordered:
			skCtx.forEachBindableChild(ch => {
				if (ch.nodeType === ENodeType.text) {
					skCtx.bindNodeToRule(ch, this);
				} else return true; //on stop le parcours des fils.
			});
		}
	}

	execActions(skCtx: SkContext, contextCard: ECard = '1') {
		const opts = skCtx.execOptions;
		if (opts.list1InsertableNodesAt !== undefined) {
			if (opts.insertableNodes1.indexOf(this) < 0) opts.insertableNodes1.push(this);
			if (opts.list2InsertableNodesAt >= 0) {
				if (opts.insertableNodes2.indexOf(this) < 0) opts.insertableNodes2.push(this);
			}
		}

		let addFindOffsetAfter;
		if (opts.findOffsetNodeType && this.structMatch(opts.findOffsetNodeType)) {
			switch (opts.findOffsetIfExistAndCardN) {
			case "reject":
				//opts.findOffset = this.isMatching(skCtx) ? -1 : 0; NON pour ne pas valuer la validit de value / data.
				opts.findOffset = skCtx.currentChild instanceof Text ? -1 : 0;
				break;
			case "start":
				if (!('findOffset' in opts)) opts.findOffset = skCtx.currentOffset;
				break;
			case "end":
				addFindOffsetAfter = true;
			}
		}

		if (opts.importContent) {
			const impCt = opts.importContent;
			let isTxt = false;
			if (impCt instanceof SkRule) isTxt = impCt.structType === ENodeType.text;
			else if (impCt instanceof Node) isTxt = impCt.nodeType === ENodeType.text;
			else isTxt = typeof impCt === "string";
			if (isTxt) {
				if ('pasteOffsetMin' in opts) {
					opts.pastePos = EPastePos.anywhere;
				} else {
					opts.importPos = {insertOffsetMin: 0, insertOffsetMax: skCtx.node.childNodes.length}
				}
			}
		}

		if (skCtx.currentChild instanceof Text) { //NON this.isMatching(skCtx) : pour ne pas valuer la validit de value / data.
			const first = skCtx.currentChild;
			if (opts.genAnnots) this._genAnnots(skCtx, contextCard);
			if (opts.autoNormXml) {
				do {
					//note : les commentaires bloquent le merge, donc on rpte aprs chaque commentaire
					let toMerge: string[];
					while (skCtx.currentChild.nextSibling instanceof Text) {
						//on fusionne les noeuds textes conscutifs
						skCtx.currentChild = skCtx.currentChild.nextSibling;
						if (!toMerge) toMerge = [skCtx.currentChild.nodeValue];
						else toMerge.push(skCtx.currentChild.nodeValue);
					}
					skCtx.currentChild = skCtx.currentChild.nextSibling; //on passe au noeud suivant.
					let str;
					if (toMerge) {
						const xaText = XA.from(first);
						str = toMerge.join('');
						if (str) opts.corrections.push(new XmlInsertMsg().init(XA.newBd(xaText).append(first.length).xa, str));
						//note : pas besoin de dcaler si suppr avant car les corrections sont initialiss en mode concurrent.
						opts.corrections.push(new XmlDeleteMsg().init(XA.incrAtDepth(xaText, -1, 1), toMerge.length));
					} else {
						if (opts.autoNormChars) skCtx.skNode.rule.skMeta.normChars(first, opts.corrections, false, null);
					}
				} while (skCtx.currentChild instanceof Text); //rpte en cas de coupure par un comment.
			} else {
				if (opts.autoNormChars) skCtx.skNode.rule.skMeta.normChars(skCtx.currentChild, opts.corrections);
				skCtx.currentChild = skCtx.currentChild.nextSibling;
				while (skCtx.currentChild instanceof Text) {
					if (opts.autoNormChars) skCtx.skNode.rule.skMeta.normChars(skCtx.currentChild, opts.corrections);
					skCtx.currentChild = skCtx.currentChild.nextSibling;
				}
			}
		} else {
			if (opts.genAnnots) this.validStr(null, contextCard, skCtx.node as Element, skCtx);
		}

		if (addFindOffsetAfter) opts.findOffset = skCtx.currentOffsetMax;
	}

	/** Validation des noeuds texte de type data / value / list (supprim par surcharge pour le type text). */
	protected _genAnnots(skCtx: SkContext, contextCard: ECard) {
		//Text.wholeText ne traverse pas les comments
		//on repart du container.
		this.validStr(skCtx.node.textContent, contextCard, skCtx.node as Element, skCtx);
	}

	matchStr(str: string, contextCard: ECard): boolean {return true}

	//matchStrList(strs: string[], contextCard: OCard): boolean {return true}

	validStr(str: string, contextCard: ECard, anchor: Element | Attr, skCtx: SkContext): void {
	}

	// validStrList(strs: string[], contextCard: OCard, anchor: Node | Attr, skCtx: SkContext): void {
	// }

	xAppendInsertables(skCtx: SkContext, listNodes: ISkStructDef[], listAttrs?: ISkStructDef[]) {
		if (listNodes?.indexOf(this) < 0) listNodes.push(this);
	}
}


/** Rule pour un noeud texte gnrique. */
export class SkRuleText extends SkRuleStr {

	get card(): ECard {return '*'}

	// @ts-ignore
	get structLabel() {return "Texte"}

	protected _genAnnots(skCtx: SkContext, contextCard: ECard) {}; //optimisation.

	indexPatterns(rulesIndex: SkRule[]) {
	}

	writeTo(o: any, mode: ESerialMode) {
	}

	readFrom(o: any, mode: ESerialMode): this {
		return SkRuleText.DEFAULT as this;
	}

	static readonly type: string = "#";

	static readonly DEFAULT = Object.freeze(new SkRuleText().linkRules(null));
}

SkRule.registry.register(SkRuleText);

/** Rule de donne type pour un noeud texte et pour valider le contenu d'un attribut (cf SkRuleAttr.valueCheck). */
export abstract class SkRuleData extends SkRuleStr {
	dataParams: Dict<string>;

	protected _card: ECard;

	get card(): ECard {return this._card || '1'}

	set card(card: ECard) {
		this._card = card;
	}

	initParams(wsType: string, dataParams: Dict<string>): this {
		this.dataParams = dataParams;
		return this;
	}

	/**
	 * Pour les data et value, on value le contenu du texte pour le matching.
	 * Ncessaire pour un choice entre data ou values.
	 */
	isMatching(skCtx: SkContext): boolean {
		const node = skCtx.currentChild;
		if (!node || !(node instanceof Text)) return false;
		return this.evalStr(skCtx.node.textContent);
	}

	matchStr(str: string, contextCard: ECard): boolean {
		if (!str && CARD.isOptionnal(CARD.combineCard(contextCard, this.card))) return true;
		return this.evalStr(str);
	}

	// matchStrList(strs: string[], contextCard: OCard): boolean {
	// 	let card = CARD.combineCard(contextCard, this.card);
	// 	if (!strs || strs.length === 0) return CARD.isOptionnal(card);
	// 	if (!this.evalStr(strs[0])) return false;
	// 	if (strs.length > 1) {
	// 		if (!CARD.isRepeatable(card)) return false;
	// 		for (let i = 1; i < strs.length; i++) {
	// 			if (!this.evalStr(strs[i])) return false;
	// 		}
	// 	}
	// 	return true;
	// }

	protected abstract evalStr(str: string): boolean;

	validStr(str: string, contextCard: ECard, anchor: Element | Attr, skCtx: SkContext): void {
		if (str == null) {
			if (CARD.isRequired(CARD.combineCard(contextCard, this.card))) skCtx.addAnnot(new SkAnnotWrongValue().init(anchor, "Valeur obligatoire"));
		} else {
			this.annotStr(str, anchor, skCtx);
		}
	}

	protected abstract annotStr(str: string, anchor: Element | Attr, skCtx: SkContext): void;

	// validStrList(strs: string[], contextCard: OCard, anchor: Node | Attr, skCtx: SkContext): void {
	// }

	getDefaultValue(): string {return this.dataParams?.defaultValue || ""}

	writeTo(o: any, mode: ESerialMode) {
		super.writeTo(o, mode);
		o.dp = this.dataParams;
	}

	readFrom(o: any, mode: ESerialMode): this {
		super.readFrom(o, mode);
		this.dataParams = o.dp;
		return this;
	}

	static dataTypes: Dict<Constructor<SkRuleData>> = {};

	/** datatypeLibrary */
	static registerDataTypes(type: string, cst: Constructor<SkRuleData>) {
		SkRuleData.dataTypes[type] = cst;
	}

	static newData(type: string, params: Jsonisable): SkRuleData {
		const Cst = SkRuleData.dataTypes[type];
		if (Cst) return new Cst().initParams(type, params);
		//SkRuleData par dfaut : SkRuleDataString
		return new SkRuleDataString();
	}
}

/** https://www.w3.org/TR/xmlschema-2/#string */
export class SkRuleDataString extends SkRuleData {

	protected minLen: number;
	protected maxLen: number;
	protected minLenWarn?: number;
	protected maxLenWarn?: number;
	protected pattern?: RegExp;
	protected patternMsg?: string;
	protected patternWarn?: RegExp;
	protected patternWarnMsg?: string;

	protected evalStr(str: string): boolean {
		this.parseParams();
		if (str.length < this.minLen || str.length > this.maxLen) return false;
		if (this.pattern && !this.pattern.test(str)) return false;
		return true;
	}

	protected annotStr(str: string, anchor: Element | Attr, skCtx: SkContext) {
		this.parseParams();
		if (str.length < this.minLen) {
			if (this.minLen === 1) {
				skCtx.addAnnot(new SkAnnotWrongValue().init(anchor, "Valeur obligatoire"));
			} else {
				skCtx.addAnnot(new SkAnnotWrongValue().init(anchor, `Valeur trop courte, infrieure  ${this.minLen} caractres`));
			}
		} else if (this.minLenWarn > 0 && str.length < this.minLenWarn) {
			skCtx.addAnnot(new SkAnnotWrongValue().init(anchor, `Valeur recommande suprieur  ${this.minLenWarn} caractres`, EAnnotLevel.warning));
		}
		if (str.length > this.maxLen) {
			skCtx.addAnnot(new SkAnnotWrongValue().init(anchor, `Valeur trop longue, suprieure  ${this.maxLen} caractres`));
		} else if (this.maxLenWarn > 0 && str.length > this.maxLenWarn) {
			skCtx.addAnnot(new SkAnnotWrongValue().init(anchor, `Valeur recommande infrieur  ${this.maxLenWarn} caractres`, EAnnotLevel.warning));
		}
		if (this.pattern && !this.pattern.test(str))
			skCtx.addAnnot(new SkAnnotWrongValue().init(anchor, this.patternMsg || `Valeur incorrecte (${this.pattern})`));
		else if (this.patternWarn && !this.patternWarn.test(str))
			skCtx.addAnnot(new SkAnnotWrongValue().init(anchor, this.patternWarnMsg || `Valeur non recommande (${this.pattern})`, EAnnotLevel.warning));
	}


	protected parseParams() {
		if (this.minLen !== undefined) return;
		const params = this.dataParams || {};
		this.minLen = params.minLength ? Number.parseInt(params.minLength, 10) : 0;
		this.maxLen = params.maxLength ? Number.parseInt(params.maxLength, 10) : Number.MAX_SAFE_INTEGER;
		if (params.minLengthWarn) this.minLenWarn = Number.parseInt(params.minLengthWarn, 10);
		if (params.maxLengthWarn) this.maxLenWarn = Number.parseInt(params.maxLengthWarn, 10);
		if (params.pattern) {
			this.pattern = new RegExp(params.pattern);
			if (params.patternMsg) this.patternMsg = params.patternMsg;
		}
		if (params.patternWarn) {
			this.patternWarn = new RegExp(params.patternWarn);
			if (params.patternWarnMsg) this.patternWarnMsg = params.patternWarnMsg;
		}
	}

	static readonly type: string = "#string";
}

SkRuleData.registerDataTypes('string', SkRuleDataString);
SkRule.registry.register(SkRuleDataString);


