/** https://www.w3.org/TR/xmlschema-2/#decimal, https://www.w3.org/TR/xmlschema-2/#integer*/
export class SkRuleDataDecimal extends SkRuleData {

	public wsType: 'integer' | 'int' | 'long' | 'decimal' | 'float';

	protected minIncl: number;
	protected maxIncl: number;
	protected fractionDigits: number;

	initParams(wsType: 'integer' | 'int' | 'long' | 'decimal' | 'float', dataParams: Dict<string>): this {
		this.wsType = wsType;
		this.dataParams = dataParams;
		return this;
	}

	protected evalStr(str: string): boolean {
		return this.valid(str) === true;
	}

	protected valid(str: string): true | 'required' | 'invalid' | 'min' | 'max' | 'frac' {
		if (!str) return 'required';
		this.parseParams();
		let val: number;
		switch (this.wsType) {
		case 'integer':
		case 'long':
		case 'int':
			if (!/^[+-]?\d+$/.test(str)) return 'invalid';
			val = Number.parseInt(str, 0);
			break;
		case 'decimal':
			if (!/^[+-]?((\d+\.?\d*)|(\d*\.\d+))$/.test(str)) return 'invalid';
			val = Number.parseFloat(str);
			const point = str.indexOf('.');
			if (point >= 0 && str.length - point - 1 > this.fractionDigits) return 'frac';
			break;
		case 'float':
			if (!/^[+-]?((\d+\.?\d*)|(\d*\.\d+))([eE]((\d+\.?\d*)|(\d*\.\d+)))?$/.test(str)) return 'invalid';
			val = Number.parseFloat(str);
			break;
		}
		if (val < this.minIncl) return 'min';
		if (val > this.maxIncl) return 'max';
		return true;
	}

	protected annotStr(str: string, anchor: Element | Attr, skCtx: SkContext) {
		switch (this.valid(str)) {
		case true:
			return;
		case 'required':
			skCtx.addAnnot(new SkAnnotWrongValue().init(anchor, "Valeur obligatoire"));
			break;
		case 'invalid':
			let t: string;
			switch (this.wsType) {
			case 'integer':
			case 'long':
			case 'int':
				t = `Valeur numrique entire incorrecte: ${str}`;
				break;
			case 'decimal':
				t = `Valeur numrique dcimale incorrecte: ${str}`;
				break;
			case "float":
				t = `Valeur numrique flottante incorrecte: ${str}`;
				break;
			}
			skCtx.addAnnot(new SkAnnotWrongValue().init(anchor, t));
			break;
		case 'min':
			skCtx.addAnnot(new SkAnnotWrongValue().init(anchor, `Valeur trop petite: ${str} < ${this.minIncl}`));
			break;
		case 'max':
			skCtx.addAnnot(new SkAnnotWrongValue().init(anchor, `Valeur trop grande: ${str} > ${this.maxIncl}`));
			break;
		case 'frac':
			const fractionDigits = this.fractionDigits;
			const val = Number.parseFloat(str);
			str = val.toLocaleString("fr");
			skCtx.addAnnot(new SkAnnotWrongValue().init(anchor, `Valeur dcimale trop fine (${fractionDigits} dcimales maximum): ${str}`));
			break;
		}
	}

	protected parseParams() {
		if (this.minIncl !== undefined) return;
		const params = this.dataParams || {};
		this.fractionDigits = params.fractionDigits ? Number.parseInt(params.fractionDigits, 10) : Number.POSITIVE_INFINITY;
		//FIXME bornes min / max pour long (https://www.w3.org/TR/xmlschema-2/#long). Faut passer en bigInt : Number.MIN_SAFE_INTEGER est insuffisant pour 'long'.
		this.minIncl = params.minInclusive ? Number.parseInt(params.minInclusive, 10) : this.wsType === 'int' ? -2147483648 : Number.NEGATIVE_INFINITY;
		this.maxIncl = params.maxInclusive ? Number.parseInt(params.maxInclusive, 10) : this.wsType === 'int' ? 2147483647 : Number.POSITIVE_INFINITY;
	}

	static readonly type: string = "#decimal";
}

SkRuleData.registerDataTypes('decimal', SkRuleDataDecimal);
SkRuleData.registerDataTypes('integer', SkRuleDataDecimal);
SkRuleData.registerDataTypes('long', SkRuleDataDecimal);
SkRuleData.registerDataTypes('int', SkRuleDataDecimal);
SkRuleData.registerDataTypes('float', SkRuleDataDecimal);
SkRule.registry.register(SkRuleDataDecimal);


/**
 * https://www.w3.org/TR/xmlschema-2/#date
 * Restrictions par rapport  xmlschema :
 * - annes ngatives non autorise.
 * - timezone non autorise.
 *
 * ATTENTION : aucune validation relle syntaxique
 */
export class SkRuleDataDate extends SkRuleDataString {
	static datePattern = /^\d{4}-\d{2}-\d{2}$/;

	static validDate(str: string): true | 'required' | 'invalid' | 'non-exist' {
		if (!str) return 'required';
		if (!SkRuleDataDate.datePattern.test(str)) return 'invalid';
		const d = new Date(str);
		if (d.getDate() !== Number.parseInt(str.substring(8, 10))
			|| d.getMonth() + 1 !== Number.parseInt(str.substring(5, 7))
			|| d.getFullYear() !== Number.parseInt(str.substring(0, 4))) return 'non-exist';
		return true;
	}

	getDefaultValue(): string {
		if (this.dataParams?.defaultValue === '#now')
			return new Date().toISOString().split("T")[0];
		return super.getDefaultValue();
	}

	protected evalStr(str: string): boolean {
		return SkRuleDataDate.validDate(str) === true;
	}

	protected annotStr(str: string, anchor: Element | Attr, skCtx: SkContext) {
		switch (SkRuleDataDate.validDate(str)) {
		case true:
			return;
		case 'required':
			skCtx.addAnnot(new SkAnnotWrongValue().init(anchor, "Valeur obligatoire"));
			break;
		case 'invalid':
			skCtx.addAnnot(new SkAnnotWrongValue().init(anchor, `Date au format YYYY-MM-JJ incorrecte: ${str}`));
			break;
		case 'non-exist':
			skCtx.addAnnot(new SkAnnotWrongValue().init(anchor, `Date inexistante dans le calendrier: ${str}`));
			break;
		}
	}

	static readonly type: string = "#date";
}

SkRuleData.registerDataTypes('date', SkRuleDataDate);
SkRule.registry.register(SkRuleDataDate);


/**
 * Correspond  <value type="string|token"/> dans Rng.
 */
export class SkRuleValue extends SkRuleData {
	dataType: 'string' | 'token';
	value: string;

	init(type: string, value: string) {
		if (type === 'token') this.dataType = type;
		this.value = value;
		return this;
	}

	get structName() {return "'" + this.value + "'"}

	getDefaultValue(): string {return this.value}

	protected evalStr(str: string): boolean {
		if (this.dataType !== 'token') return this.value === str;
		return this.value === str || this.value === DOM.txtNormToken(str);
	}

	validStr(str: string, contextCard: ECard, anchor: Element | Attr, skCtx: SkContext): void {
		//On tue le controle gnrique de str!="" de SkRuleData (pour les autres valeurs types)
		this.annotStr(str, anchor, skCtx);
	}

	protected annotStr(str: string, anchor: Element | Attr, skCtx: SkContext) {
		if (str == null) str = "";
		if (this.value === str) return;
		if (this.dataType === 'token') {
			str = DOM.txtNormToken(str);
			if (this.value === str) return;
		}
		const val = this.value;
		skCtx.addAnnot(new SkAnnotWrongValue().init(anchor, `Valeur non autorise ('${val}') : '${str}'`));
	}

	writeTo(o: any, mode: ESerialMode) {
		super.writeTo(o, mode);
		if (this.dataType === 'token') o.t = 'token';
		o.v = this.value;
	}

	readFrom(o: any, mode: ESerialMode): this {
		super.readFrom(o, mode);
		this.dataType = o.t;
		this.value = o.v;
		return this;
	}

	static readonly type: string = "#v";
}

SkRule.registry.register(SkRuleValue);

