/**
 * Rule pour un noeud comment. Jamais utilis pour valider un schma,
 * mais ncessaire pour configurer le comportement des commentaires
 * notamment pour le ISkMetaNode (SkMComment).
 */
export class SkRuleComment extends SkRuleNode {

	// @ts-ignore
	get structType() {return ENodeType.comment}

	createContent(childrenToAppend?: IJmlSet, attsToAppend?: IJmlObj): void | string {
		childrenToAppend.push({'': JML.COMMENT});
	}

	structMatch(nodeType: ENodeType, nodeName?: string): boolean {return nodeType === ENodeType.comment}

	// @ts-ignore
	get contentRule() {return SkRuleEmpty.SINGLETON}

	// @ts-ignore
	get matcher(): SkMatcherNode {return SkMatcherComment.SINGLETON}

	get card(): ECard {return '*'}

	indexPatterns(rulesIndex: SkRule[]) {
		if (this === SkRuleComment.DEFAULT) return;
		super.indexPatterns(rulesIndex);
	}

	writeTo(o: any, mode: ESerialMode) {
		if (this === SkRuleComment.DEFAULT) return;
		super.writeTo(o, mode);
		//on let o.chk  null pour diffrencier de SkRuleComment.DEFAULT.
		o.chk = null;
	}

	readFrom(o: any, mode: ESerialMode): this {
		if (o.chk === undefined) return SkRuleComment.DEFAULT as this;
		super.readFrom(o, mode);
		return this;
	}

	static readonly type: string = "!";

	static readonly DEFAULT = new SkRuleComment();
}

SkRule.registry.register(SkRuleComment);

/**
 * Rgle correspondant  un attribut.
 */
export class SkRuleAttr extends SkRule {
	static readonly type: string = "@";

	matcher: SkMatcherAttr;

	mandat: boolean;

	/** Entre d'aide. */
	helpId?: helpId

	/** Contrle de la valeur de l'attribut. */
	valueCheck: ISkRuleStr;

	/** Ref temporaire au contenu avant linkage pouvant pointer la contentRule par son name. */
	tmpValChk: string /* pour deserialisation : */ | number | ISkRuleStr;

	init(matcher: SkMatcherAttr, mandat: boolean = true, valueCheck?: ISkRuleStr) {
		this.matcher = matcher;
		this.mandat = mandat;
		this.valueCheck = valueCheck;
		return this;
	}

	initValueCheck(valueCheck: ISkRuleStr): this {
		this.valueCheck = valueCheck;
		return this;
	}

	initValueCheckByName(valueCheck: string): this {
		this.tmpValChk = valueCheck;
		return this;
	}

	// @ts-ignore
	get structName() {return this.matcher.techName}

	structMatch(nodeType: ENodeType, nodeName?: string): boolean {return nodeType === ENodeType.attribute && (nodeName == null || this.matcher.matchAttr(nodeName))}

	// @ts-ignore
	get structType() {return this.matcher.isFuzzy() ? EFuzzyType.attributes : ENodeType.attribute}

	createContent(childrenToAppend?: IJmlSet, attsToAppend?: IJmlObj): void | string {
		if (this.matcher.isFuzzy()) return this.matcher.startName;
		attsToAppend[this.matcher.startName] = this.valueCheck?.getDefaultValue() || "";
	}

	linkRules(map: ISkPatternResolver): SkRule {
		if (this.tmpValChk != null) {
			this.valueCheck = map.resolveRule(this.tmpValChk) as ISkRuleStr;
			this.tmpValChk = undefined;
		}
		if (this.valueCheck) this.valueCheck.linkRules(map);
		return this;
	}

	get card(): ECard {return this.mandat ? '1' : '?'}

	set card(card: ECard) {
		this.mandat = CARD.isRequired(card);
	}

	findRuleAttrFor(attr: string) {
		return this.matcher.matchAttr(attr) ? this : null
	}

	isMatching(skCtx: SkContext) {
		let match = false;
		skCtx.forEachBindableAttr(a => {
			if (this.matcher.matchAttr(a.nodeName)) {
				match = true;
				return true;
			}
		});
		return match;
	}

	findBestMatching(skCtx: SkContext, matchMode = ESkMatchMode.all, contextCard: ECard) {
		let found: Attr;
		if (matchMode === ESkMatchMode.all) {
			skCtx.forEachChild(ch => {
				skCtx.bindNodeToRule(ch, this.getUnknownNodeRule(skCtx.skNode, ch));
			});
			skCtx.forEachAttr((a: Attr) => {
				if (this.matcher.matchAttr(a.nodeName)) {
					skCtx.bindAttrToRule(a, this);
					found = a;
				} else {
					skCtx.bindAttrToRule(a, this.getUnknownAttRule(skCtx.skNode, a.nodeName));
				}
			});
		} else {
			skCtx.forEachBindableAttr((a: Attr) => {
				if (this.matcher.matchAttr(a.nodeName)) {
					skCtx.bindAttrToRule(a, this);
					found = a;
				}
			});
		}
		skCtx.pushRuleState(found);
	}

	execActions(skCtx: SkContext, contextCard: ECard = '1') {
		const opts = skCtx.execOptions;
		const attr = skCtx.shiftRuleState() as Attr;
		if (!attr) {
			if (this.mandat) {
				if (opts.autoComplete) {
					opts.corrections.push(new XmlStrMsg().init(XA.append(XA.from(skCtx.node), this.matcher.startName), this.valueCheck?.getDefaultValue() || ""));
				} else if (opts.genAnnots) {
					skCtx.addAnnot(new SkAnnotAttrMissing().init(skCtx.node as Element, this));
				}
			}
			if (opts.insertableAttrs) this.xAppendInsertables(skCtx, null, opts.insertableAttrs);
		} else {
			if (opts.insertableAttrs && this.matcher.isFuzzy()) this.xAppendInsertables(skCtx, null, opts.insertableAttrs);
			if (this.valueCheck && opts.genAnnots) this.valueCheck.validStr(attr.nodeValue, '1', attr, skCtx);
			if (opts.autoNormChars && this.skMeta) this.skMeta.normChars(attr, opts.corrections);
		}
		if (opts.findOffsetNodeType && this.structMatch(opts.findOffsetNodeType, opts.findOffsetNodeName)) {
			if (!opts.findOffsetNodeName) {
				//Attr libre demand (anyName) et autoris ici.
				opts.findOffset = 0;
			} else {
				//opts.findOffset = attr ? -1 : 0; NON : cet attr a pu tre capt dans un autre groupe et donc attr=null, mais impossible si existe dj dans l'elt.
				opts.findOffset = (skCtx.node as Element).hasAttribute(opts.findOffsetNodeName) ? -1 : 0;
			}
		}
	}

	/**
	 * Appel suite  l'association "virtuelle" entre un attribut et sa rgle ("virtuelle" car les attributs ne sont pas
	 * physiquement binds via un SkNode).
	 */
	execActionsOnBinding(skCtx: SkContext, attr: Attr) {
	}

	xAppendInsertables(skCtx: SkContext, listNodes: ISkStructDef[], listAttrs?: ISkStructDef[]) {
		if (listAttrs && listAttrs.indexOf(this) < 0 && this.skMeta.isInsertable(skCtx.skNode, this)) {
			const elt = skCtx.node;
			if (!(elt instanceof Element) || this.matcher.isFuzzy() || !elt.hasAttribute(this.matcher.startName)) listAttrs.push(this);
		}
	}

	indexPatterns(rulesIndex: SkRule[]) {
		if (Object.isFrozen(this)) return;
		if (this.id === undefined) {
			this.id = rulesIndex.length;
			rulesIndex.push(this);
			if (this.valueCheck) this.valueCheck.indexPatterns(rulesIndex);
		}
	}

	writeTo(o: any, mode: ESerialMode) {
		super.writeTo(o, mode);
		o.matcher = Serializable.serialSerializable(this.matcher, mode);
		if (this.valueCheck != null) o.chk = SkRule.serialRefRule(this.valueCheck, mode);
		if (this.mandat) o.mandat = true;
	}

	readFrom(o: any, mode: ESerialMode): this {
		super.readFrom(o, mode);
		this.matcher = Serializable.deserialSerializable(o.matcher, mode, SkMatcherAttr.registry);
		if (o.chk) this.tmpValChk = SkRule.deserialRefRule(o.chk, mode) as ISkRuleStr | number;
		this.mandat = o.mandat === true;
		return this;
	}
}

SkRule.registry.register(SkRuleAttr);

export class SkRuleAttrUnknown extends SkRuleAttr {
	static readonly type = "@?";

	initUnknown(matcher: SkMatcherAttr = SkMatcherAnyAttr.SINGLETON, skMeta = SKMETALIB.getMetaNode('@')): this {
		this.matcher = matcher;
		this.skMeta = skMeta;
		return this;
	}

	findBestMatching(skCtx: SkContext, matchMode: ESkMatchMode, contextCard: ECard) {
		//Les rules pour les attr inconnus ne sont pas intgrs dans le jeu de rgles de SkRuleNode.contentRule
		//Ne sera donc jamais appele
		console.log("findBestMatching::SkRuleAttrUnknown:", skCtx);
	}

	execActions(skCtx: SkContext, contextCard: ECard) {
	}

	execActionsOnBinding(skCtx: SkContext, attr: Attr) {
		if (skCtx.skNode.rule instanceof SkRuleEltUnknown) return; //Aucun traitement si on n'est pas un attr unkonwn root.
		if (skCtx.execOptions.autoCleanup) skCtx.execOptions.corrections.push(new XmlStrMsg().init(XA.from(attr), null));
		else if (skCtx.execOptions.genAnnots) skCtx.addAnnot(new SkAnnotAttrUnknown().init(attr));
	}

	writeTo(o: any, mode: ESerialMode) {
		if (this === SkRuleAttrUnknown.DEFAULT) return;
		super.writeTo(o, mode);
		if (this.matcher != SkMatcherAnyAttr.SINGLETON) o.matcher = Serializable.serialSerializable(this.matcher, mode);
		if (this.valueCheck != null) o.chk = SkRule.serialRefRule(this.valueCheck, mode);
		o.mandat = this.mandat;
	}

	readFrom(o: any, mode: ESerialMode): this {
		if (o.mandat === undefined) return SkRuleAttrUnknown.DEFAULT as this;
		super.readFrom(o, mode);
		if (o.matcher) this.matcher = Serializable.deserialSerializable(o.matcher, mode, SkMatcherAttr.registry);
		if (o.chk) this.tmpValChk = SkRule.deserialRefRule(o.chk, mode) as ISkRuleStr | number;
		this.mandat = o.mandat === true;
		return this;
	}

	static readonly DEFAULT = new SkRuleAttrUnknown();
}

SkRule.registry.register(SkRuleAttrUnknown);


export class SkRuleChoice extends SkRuleDirective implements ISkRuleStr {
	static readonly type = "|";

	/** */
	static readonly ANY = new SkRuleChoice();

	// @ts-ignore
	get structName() {
		//if (this.uniqueName != null) return this.uniqueName;
		if (!this._builtName) {
			const buf = ["("];
			this.subRules.forEach((r, i) => {
				const card = CARD.combineCard(this.overSubCard(i, '1'), r.card);
				if (card !== '1') {
					buf.push(r.structName, card, "|");
				} else {
					buf.push(r.structName, "|");
				}
			});
			buf[buf.length - 1] = ")";
			this._builtName = buf.join('');
		}
		return this._builtName;
	}

	// @ts-ignore
	get structType() {return EDirectiveType.choice}


	autoCreate?: "first";

	initAutoCreate(v: string | null): this {
		this.autoCreate = v === "first" ? "first" : undefined;
		return this;
	}

	/** Pour valeur par dfaut d'une num. */
	defaultValue?: string

	initDefaultValue(d?: string): this {
		if (d) this.defaultValue = d;
		return this;
	}

	findBestMatching(skCtx: SkContext, matchMode = ESkMatchMode.all, contextCard: ECard = '1') {
		const card = CARD.combineCard(contextCard, this.card);
		const stateIdx = skCtx.reserveRuleState();
		let found = this.xFindBestMatchingSub(skCtx, card);
		if (matchMode === ESkMatchMode.all) {
			//Directive racine, tant qu'il reste des nodes inconnus, on les exclut un  un et on recommence.
			while (skCtx.currentChild) {
				skCtx.bindUnknownNodeToRuleAndReset(skCtx.currentChild, this.getUnknownNodeRule(skCtx.skNode, skCtx.currentChild), stateIdx);
				found = this.xFindBestMatchingSub(skCtx, card);
			}
			skCtx.forEachBindableAttr(a => {
				skCtx.bindAttrToRule(a, this.getUnknownAttRule(skCtx.skNode, a.nodeName));
			});
		}
		skCtx.setRuleState(stateIdx, found);
	}

	xFindBestMatchingSub(skCtx: SkContext, card: ECard) {
		let found: (SkRule | Node | number)[];
		repeat: do {
			for (let i = 0; i < this.subRules.length; i++) {
				const rule = this.subRules[i];
				if (rule.isMatching(skCtx)) {
					if (!found) found = [rule, skCtx.currentChild];
					else found.push(rule, skCtx.currentChild);
					rule.findBestMatching(skCtx, ESkMatchMode.ordered, this.overSubCard(i, card));
					found.push(skCtx.currentOffsetMax);
					continue repeat;
				}
			}
			break;
		} while (CARD.isRepeatable(card));
		return found;
	}

	isMatching(skCtx: SkContext) {
		for (let i = 0; i < this.subRules.length; i++) {
			if (this.subRules[i].isMatching(skCtx)) return true;
		}
		return false;
	}

	execActions(skCtx: SkContext, contextCard: ECard = '1') {
		const card = CARD.combineCard(contextCard, this.card);
		const found = skCtx.shiftRuleState() as Array<SkRule | Node | number>;
		const opts = skCtx.execOptions;
		const list1InsertableNodesAt = opts.list1InsertableNodesAt;
		const insertableAttrs = opts.insertableAttrs;
		const importContent = opts.importContent;
		let addFindOffsetAfter;
		if (found == null || CARD.isRepeatable(card)) {
			if (list1InsertableNodesAt !== undefined) {
				const offsetMin = skCtx.currentOffsetMin;
				const offsetMax = found ? (found[found.length - 1] as number) : skCtx.currentOffsetMax;
				if (list1InsertableNodesAt === -1 || (offsetMin <= list1InsertableNodesAt && offsetMax >= list1InsertableNodesAt)) {
					this.xAppendInsertables(skCtx, opts.insertableNodes1);
				}
				const insPoint2 = opts.list2InsertableNodesAt;
				if (insPoint2 >= 0 && offsetMin <= insPoint2 && offsetMax >= insPoint2) {
					this.xAppendInsertables(skCtx, opts.insertableNodes2);
				}
				//On kill l'insertion des noeuds insrables.
				opts.list1InsertableNodesAt = undefined;
			}
			if (insertableAttrs) {
				this.xAppendInsertables(skCtx, null, insertableAttrs);
				//On kill l'insertion des noeuds insrables.
				opts.insertableAttrs = null;
			}
			if (opts.findOffsetNodeType && this.structMatch(opts.findOffsetNodeType, opts.findOffsetNodeName)) {
				switch (CARD.isRepeatable(card) ? opts.findOffsetIfExistAndCardN : 'reject') {
				case "reject":
					opts.findOffset = this.isInFound(found, opts.findOffsetNodeType, opts.findOffsetNodeName) ? -1 : skCtx.currentOffset;
					break;
				case "start":
					if (!('findOffset' in opts)) opts.findOffset = skCtx.currentOffset;
					break;
				case "end":
					addFindOffsetAfter = true;
				}
			}
		}
		if (importContent) {
			const subCard = importContent instanceof SkRule ? this.getRealCardSubRule(importContent) :
				importContent instanceof Node ? this.getRealCardSubNode(importContent.nodeType, importContent.nodeName) :
					this.getRealCardSubNode(JML.jmlNode2nodeType(importContent), JML.jmlNode2name(importContent));
			if (subCard !== undefined) {
				//importContent est bien autoris ici.
				if ('pasteOffsetMin' in opts) {
					if (opts.pasteOffsetMin >= skCtx.currentOffsetMin && opts.pasteOffsetMax <= (found ? (found[found.length - 1] as number) : skCtx.getOffsetMaxAfterCurrent(0))) {
						//A Affiner...
						if (CARD.isRepeatable(subCard)) {
							opts.pastePos = EPastePos.anywhere;
						} else {
							opts.pastePos = EPastePos.replace;
						}
					}
				} else {
					if (CARD.isRepeatable(subCard)) {
						if (found) {
							const maxOffset = found[found.length - 1] as number;
							opts.importPos = {insertOffsetMin: skCtx.currentOffsetMin, insertOffsetMax: maxOffset, replaceChildren: [skCtx.currentOffset]};
							let n = skCtx.currentChild;
							for (let i = skCtx.currentOffset + 1; i < maxOffset; i++) {
								n = n.nextSibling;
								if (n.nodeType === ENodeType.element) opts.importPos.replaceChildren.push(i);
							}
						} else {
							opts.importPos = {insertOffsetMin: skCtx.currentOffsetMin, insertOffsetMax: skCtx.currentOffsetMax};
						}
					} else if (found) {
						opts.importPos = {replaceChildren: [skCtx.currentOffset]};
					} else {
						opts.importPos = {insertOffsetMin: skCtx.currentOffset, insertOffsetMax: skCtx.currentOffset};
					}
				}
			}
			//On kill l'eval de l'import de content pour les subRules
			opts.importContent = null;
		}
		if (found == null) {
			if (CARD.isRequired(card)) {
				let genAn = opts.genAnnots;
				if (opts.autoComplete) {
					//console.log("AutoComplete....");
					if (!this.isStrChoice()) {
						//Si cration par dfaut forc
						const content: IJmlSet = [];
						this.createContent(content, {});
						//XXX cration des attributs ?
						if (content.length > 0) {
							opts.corrections.push(new XmlInsertMsg().init(XA.append(XA.from(skCtx.node), skCtx.currentOffset), content));
							genAn = false;
						}
					}
				}
				if (genAn) {
					let i: number;
					if (skCtx.newAnnots && this.isStrChoice() && (i = skCtx.newAnnots.findIndex((e) => e instanceof SkAnnotTextForbidden)) >= 0) {
						//On est un choice de string (data dans un texte) et on a dtect un noeud texte interdit.
						//On remplace le couple d'annot "texte interdit" + "texte manquant", par une annot de type WrongValue
						skCtx.newAnnots[i] = new SkAnnotWrongValue().init(skCtx.node as Element, "Valeur incorrecte");
					} else {
						skCtx.addAnnot(new SkAnnotMissing().init(SkAnnotMissing.TYPE_choiceMissing, skCtx.node, skCtx.currentOffset, this));
					}
				}
			}
		} else {
			for (let i = 0; i < found.length; i = i + 3) {
				skCtx.currentChild = found[i + 1] as Node;
				(found[i] as SkRule).execActions(skCtx, card);
			}
		}
		if (addFindOffsetAfter) opts.findOffset = skCtx.currentOffsetMax;
		//On restaure le kill de la construction des noeuds insrables.
		if (list1InsertableNodesAt !== undefined) opts.list1InsertableNodesAt = list1InsertableNodesAt;
		if (insertableAttrs) opts.insertableAttrs = insertableAttrs;
		if (importContent) opts.importContent = importContent;
	}

	isInFound(found: Array<SkRule | Node | number>, nodeType: ENodeType, nodeName: string): boolean {
		if (!found) return false;
		for (let i = 0; i < found.length; i = i + 3) {
			if ((found[i] as SkRule).structMatch(nodeType, nodeName)) return true;
		}
		return false;
	}

	createContent(childrenToAppend?: IJmlSet, attsToAppend?: IJmlObj, guardStack?: SkRuleElt[]): void | string {
		if (this.autoCreate === "first" && this.subRules.length > 0) {
			const lenCh = childrenToAppend.length;
			const lenAtts = Object.getOwnPropertyNames(attsToAppend).length;
			for (let r of this.subRules) {
				//On atteint le 1er creatable (noAutoComplete != true), ie on saute les BlackHole par exemple
				r.createContent(childrenToAppend, attsToAppend, guardStack);
				if (lenCh !== childrenToAppend.length || lenAtts !== Object.getOwnPropertyNames(attsToAppend).length) break; //Un ajout trouv, on arrte
			}
		} else if (this.subRules.length === 1) {
			const r = this.subRules[0];
			//On ajoute le 1er lement obligatoire du choice.
			if (CARD.isRequired(CARD.combineCard(this.overSubCard(0, '1'), r.card))) {
				r.createContent(childrenToAppend, attsToAppend, guardStack);
			}
		}
	}

	getDefaultValue(): string {return this.defaultValue || ""}

	xAppendInsertables(skCtx: SkContext, listNodes: ISkStructDef[], listAttrs?: ISkStructDef[]) {
		for (let i = 0; i < this.subRules.length; i++) {
			this.subRules[i].xAppendInsertables(skCtx, listNodes, listAttrs);
			// let rule = this.subRules[i];
			// if (rule instanceof SkRuleElt) {
			// 	listNodes.push(rule);
			// } else if (rule instanceof SkRuleAttr) {
			// 	if (listAttrs) listAttrs.push(rule);
			// } else if (rule instanceof SkRuleChoice) {
			// 	rule.xAppendInsertables(listNodes, listAttrs);
			// }
		}
	}

	isStrChoice() {
		if (this._isStrChoice === undefined) this._isStrChoice = this.findRule((r) => !isSkRuleStr(r)) == null;
		return this._isStrChoice;
	}

	protected _isStrChoice: boolean;

	matchStr(str: string, contextCard: ECard): boolean {
		const card = CARD.combineCard(contextCard, this.card);
		for (let i = 0; i < this.subRules.length; i++) {
			if ((this.subRules[i] as ISkRuleStr).matchStr(str, this.overSubCard(i, card))) return true;
		}
		return false;
	}

	validStr(str: string, contextCard: ECard, anchor: Element | Attr, skCtx: SkContext): void {
		if (!this.matchStr(str, contextCard)) {
			if (!str) {
				//noeud texte manquant ou == "", ou attr == "" interdit.
				skCtx.addAnnot(new SkAnnotWrongValue().init(anchor, `Valeur obligatoire`));
			} else {
				skCtx.addAnnot(new SkAnnotWrongValue().init(anchor, this.structLabel ? `Valeur incorrecte (${this.structLabel})` : `Valeur incorrecte`));
			}
		}
	}

	freezeRule() {
		this.isStrChoice(); //compute _isStrChoice
		Object.freeze(this);
	}
}

SkRule.registry.register(SkRuleChoice);

export class SkRuleUGroup extends SkRuleDirective {
	static readonly type = "&";

	// @ts-ignore
	get structName() {
		//if (this.uniqueName != null) return this.uniqueName;
		if (!this._builtName) {
			const buf = ["("];
			this.subRules.forEach((r, i) => {
				const card = this.overSubCard(i, '1');
				if (card !== '1') {
					buf.push(r.structName, card, "&");
				} else {
					buf.push(r.structName, "&");
				}
			});
			buf[buf.length - 1] = ")";
			this._builtName = buf.join('');
		}
		return this._builtName;
	}

	// @ts-ignore
	get structType() {return EDirectiveType.unorderedGroup}

	findBestMatching(skCtx: SkContext, matchMode = ESkMatchMode.all, contextCard: ECard = '1') {
		const card = CARD.combineCard(contextCard, this.card);
		const stateIdx = skCtx.reserveRuleState();
		let found = this.xFindBestMatchingSub(skCtx, card);
		if (matchMode === ESkMatchMode.all) {
			//Directive racine, tant qu'il reste des nodes inconnus, on les exclut un  un et on recommence.
			while (skCtx.currentChild) {
				skCtx.bindUnknownNodeToRuleAndReset(skCtx.currentChild, this.getUnknownNodeRule(skCtx.skNode, skCtx.currentChild), stateIdx);
				found = this.xFindBestMatchingSub(skCtx, card);
			}
			skCtx.forEachBindableAttr(a => {
				skCtx.bindAttrToRule(a, this.getUnknownAttRule(skCtx.skNode, a.nodeName));
			});
		}
		skCtx.setRuleState(stateIdx, found);
	}

	xFindBestMatchingSub(skCtx: SkContext, card: ECard) {
		let found: (SkRule | Node)[];
		if (CARD.isRequired(card) || this.isMatching(skCtx)) {
			//clone la liste des rules pour limiter chaque rule  une seule utilisation.
			const countMatch: number[] = [];
			eachNode: while (skCtx.currentChild) {
				for (let i = 0; i < this.subRules.length; i++) {
					const rule = this.subRules[i];
					const count = countMatch[i] || 0;
					const ruleCard = this.overSubCard(i, card);
					if (count == 0 || CARD.isRepeatable(CARD.combineCard(ruleCard, rule.card))) {
						if (rule.isMatching(skCtx)) {
							countMatch[i] = count + 1;
							if (!found) found = [rule, skCtx.currentChild];
							else found.push(rule, skCtx.currentChild);
							rule.findBestMatching(skCtx, ESkMatchMode.ordered, ruleCard);
							continue eachNode;
						}
					}
				}
				//toutes les rules de l'unorderedGroup ont t pass en revue, on ne progresse plus.
				break;
			}
			if (found && CARD.isRequired(card)) {
				//au moins lment a match, on mmorise ceux qui n'ont pas match (gestion du missing).
				for (let i = 0; i < this.subRules.length; i++) {
					//on mmorise les rules qui seront flagues manquantes
					const rule = this.subRules[i];
					const ruleCard = this.overSubCard(i, card);
					if (!countMatch[i] && CARD.isRequired(CARD.combineCard(ruleCard, rule.card))) {
						found.push(rule, skCtx.currentChild);
						rule.findBestMatching(skCtx, ESkMatchMode.ordered, ruleCard);
					}
				}
			}
		}
		if (CARD.isRepeatable(card)) {// + ou *
			eachNode: while (skCtx.currentChild) {
				for (let i = 0; i < this.subRules.length; i++) {
					const rule = this.subRules[i];
					if (rule.isMatching(skCtx)) {
						if (!found) found = [rule, skCtx.currentChild];
						else found.push(rule, skCtx.currentChild);
						rule.findBestMatching(skCtx, ESkMatchMode.ordered, this.overSubCard(i, card));
						continue eachNode;
					}
				}
				break;
			}
		}
		return found;
	}

	/**
	 * On value si le noeud courant du contexte match une des rules de l'unorderedGroup.
	 */
	isMatching(skCtx: SkContext) {
		if (!skCtx.currentChild) return false;
		for (let i = 0; i < this.subRules.length; i++) {
			if (this.subRules[i].isMatching(skCtx)) return true;
		}
		return false;
	}

	execActions(skCtx: SkContext, contextCard: ECard = '1') {
		const card = CARD.combineCard(contextCard, this.card);
		const opts = skCtx.execOptions;
		const found = skCtx.shiftRuleState() as Array<SkRule | Node>;
		if (opts.list1InsertableNodesAt !== undefined) {
			console.log("TODO listInsertableNodes for SkRuleUGroup");
		}
		let addFindOffsetAfter;
		const importContent = opts.importContent;
		if (opts.findOffsetNodeType && this.structMatch(opts.findOffsetNodeType, opts.findOffsetNodeName)) {
			const structFound = this.isInFound(found, opts.findOffsetNodeType, opts.findOffsetNodeName);
			if (!structFound || CARD.isRepeatable(card)) {
				switch (CARD.isRepeatable(card) ? opts.findOffsetIfExistAndCardN : 'reject') {
				case "reject":
					opts.findOffset = structFound ? -1 : skCtx.currentOffset;
					break;
				case "start":
					if (!('findOffset' in opts)) opts.findOffset = skCtx.currentOffset;
					break;
				case "end":
					addFindOffsetAfter = true;
				}
			}
		}
		if (importContent) {
			const subCard = importContent instanceof SkRule ? this.getRealCardSubRule(importContent) :
				importContent instanceof Node ? this.getRealCardSubNode(importContent.nodeType, importContent.nodeName) :
					this.getRealCardSubNode(JML.jmlNode2nodeType(importContent), JML.jmlNode2name(importContent));
			if (subCard !== undefined) {
				//importContent est bien autoris ici.
				if ('pasteOffsetMin' in opts) {
					if (opts.pasteOffsetMin >= skCtx.currentOffsetMin && opts.pasteOffsetMax <= skCtx.getOffsetMaxAfterCurrent(found ? found.length / 2 : 0)) {
						//A Affiner...
						if (CARD.isRepeatable(card)) {
							opts.pastePos = EPastePos.anywhere;
						} else {
							console.trace("TODO Si importContent isInFound, 0 ou 'replace' (si skCtx.currentChild est la mme Rule que importContent) Sinon EPastePos.before | EPastePos.replace | EPastePos.after.");
							//opts.pastePos = EPastePos.replace;
						}
					}
					//On kill l'eval de limport de content pour les subRules
					opts.importContent = null;
				} else {
					if (CARD.isRepeatable(card)) {
						opts.importPos = {insertOffsetMin: skCtx.currentOffsetMin, insertOffsetMax: skCtx.getOffsetMaxAfterCurrent(found ? found.length / 2 : 0)};
					} else if (found) {
						opts.importPos = {replaceChildren: [skCtx.currentOffset]};
					} else {
						opts.importPos = {insertOffsetMin: skCtx.currentOffset, insertOffsetMax: skCtx.currentOffset};
					}
				}
			}
		}
		if (found == null) {
			if (opts.genAnnots && CARD.isRequired(card)) {
				skCtx.addAnnot(new SkAnnotMissing().init(SkAnnotMissing.TYPE_ugroupMissing, skCtx.node, skCtx.currentOffset, this));
			}
		} else {
			for (let i = 0; i < found.length; i = i + 2) {
				skCtx.currentChild = found[i + 1] as Node;
				(found[i] as SkRule).execActions(skCtx, card);
			}
		}
		if (addFindOffsetAfter) opts.findOffset = skCtx.currentOffsetMax;
		if (importContent) opts.importContent = importContent;
	}

	isInFound(found: Array<SkRule | Node>, nodeType: ENodeType, nodeName: string): boolean {
		if (!found) return false;
		for (let i = 0; i < found.length; i = i + 2) {
			if ((found[i] as SkRule).structMatch(nodeType, nodeName)) return true;
		}
		return false;
	}

	createContent(childrenToAppend?: IJmlSet, attsToAppend?: IJmlObj, guardStack?: SkRuleElt[]): void | string {
		this.subRules.forEach((r, i) => {
			if (CARD.isRequired(CARD.combineCard(this.overSubCard(i, '1'), r.card))) {
				r.createContent(childrenToAppend, attsToAppend, guardStack);
			}
		});
	}

	xAppendInsertables(skCtx: SkContext, listNodes: ISkStructDef[], listAttrs?: ISkStructDef[]) {
		for (let i = 0; i < this.subRules.length; i++) {
			this.subRules[i].xAppendInsertables(skCtx, listNodes, listAttrs);
		}
	}
}

SkRule.registry.register(SkRuleUGroup);

export class SkRuleGroup extends SkRuleDirective {
	static readonly type = ",";

	// @ts-ignore
	get structName() {
		//if (this.uniqueName != null) return this.uniqueName;
		if (!this._builtName) {
			const buf = ["("];
			this.subRules.forEach((r, i) => {
				const card = this.overSubCard(i, '1');
				if (card !== '1') {
					buf.push(r.structName, card, ",");
				} else {
					buf.push(r.structName, ",");
				}
			});
			buf[buf.length - 1] = ")";
			this._builtName = buf.join('');
		}
		return this._builtName;
	}

	// @ts-ignore
	get structType() {return EDirectiveType.group}

	findBestMatching(skCtx: SkContext, matchMode = ESkMatchMode.all, contextCard: ECard = '1') {
		const card = CARD.combineCard(contextCard, this.card);
		const stateIdx = skCtx.reserveRuleState();
		let found: Node[];
		if (matchMode === ESkMatchMode.all) {
			found = this.xFindBestMatchingSub(skCtx, card);
			//Directive racine, tant qu'il reste des nodes inconnus, on les exclut un  un et on recommence.
			while (skCtx.currentChild) {
				skCtx.bindUnknownNodeToRuleAndReset(skCtx.currentChild, this.getUnknownNodeRule(skCtx.skNode, skCtx.currentChild), stateIdx);
				found = this.xFindBestMatchingSub(skCtx, card);
			}
			skCtx.forEachBindableAttr(a => {
				skCtx.bindAttrToRule(a, this.getUnknownAttRule(skCtx.skNode, a.nodeName));
			});
		} else {
			found = this.xFindBestMatchingSub(skCtx, card);
		}
		skCtx.setRuleState(stateIdx, found);
	}

	buildRuleAfter(from: ISkRuleObj, ctxCard: ECard = '1'): SkRule {
		const realCard = CARD.combineCard(this.card, ctxCard);
		if (CARD.isRepeatable(realCard)) return this;
		let result: SkRule;
		if (this.rulesAfter) {
			result = this.rulesAfter.get(from);
			if (result) return result;
		} else {
			this.rulesAfter = new Map();
		}
		//On cre un SkRule ddi avec les rules suivantes.
		let i = 0;
		let firstSubR: SkRule;
		for (; i < this.subRules.length; i++) {
			const subR = this.subRules[i];
			if (subR.findRule((r) => r === from)) {
				//Trouv
				firstSubR = subR.buildRuleAfter(from, this.overSubCard(i, subR.card));
				if (i === this.subRules.length - 1) result = firstSubR;
				else if (firstSubR === SkRuleEmpty.SINGLETON && i === this.subRules.length - 2) result = this.subRules[this.subRules.length - 1];
				else {
					const rules = [] as SkRule[];
					if (firstSubR !== SkRuleEmpty.SINGLETON) rules.push(firstSubR);
					for (i++; i < this.subRules.length; i++) rules.push(this.subRules[i]);
					result = new SkRuleGroup().initSubRules(rules);
				}
				break;
			}
		}
		if (!result) result = SkRuleEmpty.SINGLETON;
		this.rulesAfter.set(from, result);
		return result;
	}

	/** Mmoire des ruleAfter calculs (voir instancis) en fonction de chaque from. */
	protected rulesAfter: Map<ISkRuleObj, SkRule>;

	checkSchemaOrder(eltName1: string, eltName2: string): boolean | undefined {
		let pos1 = null;
		let pos2 = null;
		for (let i = 0, s = this.subRules.length; i < s; i++) {
			const subR = this.subRules[i];
			if (pos1 === null && subR.structMatch(ENodeType.element, eltName1)) pos1 = i;
			if (pos2 === null && subR.structMatch(ENodeType.element, eltName2)) pos2 = i;
			if (pos1 !== null && pos2 !== null) {
				if (pos1 === pos2) return subR.checkSchemaOrder(eltName1, eltName2);
				return pos1 < pos2;
			}
		}
		return undefined;
	}

	xFindBestMatchingSub(skCtx: SkContext, card: ECard): null | Node[] {
		let found: Node[]; //1er noeud du groupe.
		if (CARD.isRequired(card) || this.isMatching(skCtx)) {
			//FIXME incorrect pour des groupes d'attributs
			const ch = skCtx.currentChild;
			if (ch) {
				if (!found) found = [ch];
				else found.push(ch);
			}
			for (let i = 0; i < this.subRules.length; i++) {
				this.subRules[i].findBestMatching(skCtx, ESkMatchMode.ordered, this.overSubCard(i, '1'));
			}
		}
		while (CARD.isRepeatable(card) && this.isMatching(skCtx)) {
			//FIXME incorrect pour des groupes d'attributs
			if (!found) found = [skCtx.currentChild];
			else found.push(skCtx.currentChild);
			for (let i = 0; i < this.subRules.length; i++) {
				this.subRules[i].findBestMatching(skCtx, ESkMatchMode.ordered, this.overSubCard(i, '1'));
			}
		}
		return found;
	}

	isMatching(skCtx: SkContext) {
		for (let i = 0; i < this.subRules.length; i++) {
			const rule = this.subRules[i];
			if (rule.isMatching(skCtx)) return true;
			//cette rule ne match pas, si elle est obligaroire, le group ne matche pas.
			if (CARD.isRequired(this.overSubCard(i, rule.card))) return false;
		}
		return false;
	}

	execActions(skCtx: SkContext, contextCard: ECard = '1') {
		const card = CARD.combineCard(contextCard, this.card);
		const opts = skCtx.execOptions;
		const found = skCtx.shiftRuleState() as Node[];
		let addFindOffsetAfter;
		if (found == null || CARD.isRepeatable(card)) {
			if (opts.list1InsertableNodesAt !== undefined) {
				const min = skCtx.currentOffsetMin;
				const max = skCtx.getOffsetMaxAfterCurrent(found ? found.length : 0);
				const insPoint1 = opts.list1InsertableNodesAt;
				if (insPoint1 === -1 || (min <= insPoint1 && max >= insPoint1)) {
					this.xAppendInsertables(skCtx, opts.insertableNodes1);
				}
				const insPoint2 = opts.list2InsertableNodesAt;
				if (insPoint2 >= 0 && min <= insPoint2 && max >= insPoint2) {
					this.xAppendInsertables(skCtx, opts.insertableNodes2);
				}
			}
			if (opts.insertableAttrs) {
				this.xAppendInsertables(skCtx, null, opts.insertableAttrs);
			}
			if (opts.findOffsetNodeType && this.structMatch(opts.findOffsetNodeType, opts.findOffsetNodeName)) {
				switch (CARD.isRepeatable(card) ? opts.findOffsetIfExistAndCardN : 'reject') {
				case "reject":
					opts.findOffset = found ? -1 : skCtx.currentOffset;
					break;
				case "start":
					if (!('findOffset' in opts)) opts.findOffset = skCtx.currentOffset;
					break;
				case "end":
					addFindOffsetAfter = true;
				}
			}
		}
		if (!found) {
			if (CARD.isRequired(card)) {
				// NON, pas de groupMissing : le groupe peut-tre required, mais tous ses fils optionnels, on doit descendre dans les fils pour cette eval.
				// if (opts.genAnnots) skCtx.addAnnot(new SkAnnotMissing().init(SkAnnotMissing.TYPE_groupMissing, skCtx.node, skCtx.currentOffset, this));
				this.subRules.forEach((r, i) => {
					r.execActions(skCtx, this.overSubCard(i, '1'));
				});
			} else {
				const importContent = opts.importContent;
				if (importContent) {
					const subCard = importContent instanceof SkRule ? this.getRealCardSubRule(importContent) :
						importContent instanceof Node ? this.getRealCardSubNode(importContent.nodeType, importContent.nodeName) :
							this.getRealCardSubNode(JML.jmlNode2nodeType(importContent), JML.jmlNode2name(importContent));
					if (subCard !== undefined) {
						//importContent est bien autoris ici.
						if ('pasteOffsetMin' in opts) {
							if (opts.pasteOffsetMin >= skCtx.currentOffsetMin && opts.pasteOffsetMax <= skCtx.currentOffsetMax) {
								opts.pastePos = EPastePos.replace;
							}
						} else {
							opts.importPos = {insertOffsetMin: skCtx.currentOffsetMin, insertOffsetMax: skCtx.currentOffsetMax};
						}
					}
				}
			}
		} else if (skCtx.execOptions.findOffsetNodeType) {
			//On cherche un point d'insertion
			const backup = opts.findOffsetInGroup;
			for (let i = 0; i < found.length; i++) {
				//On parcourt chaque groupe.
				skCtx.currentChild = found[i]; //1er noeud du groupe.
				opts.findOffsetInGroup = 'start';
				this.subRules.forEach((r, i) => {
					let found: boolean;
					const curr = skCtx.currentChild;
					if (curr && r.structMatch(curr.nodeType, curr.nodeName)) {
						opts.findOffsetInGroup = undefined;
						found = true;
					}
					r.execActions(skCtx, this.overSubCard(i, '1'));
					if (found) opts.findOffsetInGroup = 'start';
				});
			}
			opts.findOffsetInGroup = backup; //retsore pour le contexte parent.
		} else {
			for (let i = 0; i < found.length; i++) {
				skCtx.currentChild = found[i];
				this.subRules.forEach((r, i) => {
					r.execActions(skCtx, this.overSubCard(i, '1'))
				});
			}
		}
		if (addFindOffsetAfter) opts.findOffset = skCtx.currentOffsetMax;
	}

	createContent(childrenToAppend?: IJmlSet, attsToAppend?: IJmlObj, guardStack?: SkRuleElt[]): void | string {
		this.subRules.forEach((r, i) => {
			if (CARD.isRequired(CARD.combineCard(this.overSubCard(i, '1'), r.card))) {
				r.createContent(childrenToAppend, attsToAppend, guardStack);
			}
		});
	}

	xAppendInsertables(skCtx: SkContext, listNodes: ISkStructDef[], listAttrs?: ISkStructDef[]) {
		let i = 0;
		if (listNodes) for (; i < this.subRules.length; i++) {
			//On ajoute les rules filles (node et attr) jusqu'au premier node fils obligatoire (hors attributs).
			const rule = this.subRules[i];
			const offs = listNodes.length;
			rule.xAppendInsertables(skCtx, listNodes, listAttrs);
			if (listNodes.length > offs && CARD.isRequired(rule.card)) break;
		}
		if (listAttrs) for (; i < this.subRules.length; i++) {
			//On ajoute tous les autres attributs dans le groupe.
			this.subRules[i].xAppendInsertables(skCtx, null, listAttrs);
		}
	}
}

SkRule.registry.register(SkRuleGroup);

export class SkRuleEmpty extends SkRuleDirective {
	static readonly type = "-";

	subRules = SkRuleEmpty.EMPTY_RULES;

	// @ts-ignore
	get structName() {return ""}

	// @ts-ignore
	get structType() {return EDirectiveType.empty}

	// @ts-ignore
	get structLabel() {return "Vide"}

	linkRules(map: ISkPatternResolver): SkRule {return this}

	isMatching(skCtx: SkContext): boolean {
		//console.log("Empty rule should not be used in complex rules", DOM.debug(skCtx.node));-
		return !skCtx.currentChild && (!(skCtx.node instanceof Element) || skCtx.node.attributes.length === 0);
	}

	findBestMatching(skCtx: SkContext, matchMode = ESkMatchMode.all, contextCard: ECard = '1') {
		if (matchMode === ESkMatchMode.all) {
			skCtx.forEachChild(ch => {
				skCtx.bindNodeToRule(ch, this.getUnknownNodeRule(skCtx.skNode, ch));
			});
			skCtx.forEachAttr(a => {
				skCtx.bindAttrToRule(a, this.getUnknownAttRule(skCtx.skNode, a.nodeName));
			});
		}
	}

	indexPatterns(rulesIndex: SkRule[]) {
		//singleton jamais index
	}

	createContent(childrenToAppend?: IJmlSet, attsToAppend?: IJmlObj): void | string {}

	writeTo(o: any, mode: ESerialMode) {}

	readFrom(o: any, mode: ESerialMode): this {
		return SkRuleEmpty.SINGLETON as this;
	}

	static readonly EMPTY_RULES = Object.freeze([]) as SkRule[];
	static readonly SINGLETON = Object.freeze(new SkRuleEmpty());
}

SkRule.registry.register(SkRuleEmpty);

/** Init des SkRule par dfaut appel aprs chargement des modules pour problme de cycles. */
export function initSkRuleDefaults() {
	if (!Object.isFrozen(SkRuleEltUnknown.DEFAULT)) {
		Object.freeze(SkRuleEltUnknown.DEFAULT.initUnknown());
		Object.freeze(SkRuleAttrUnknown.DEFAULT.initUnknown());
		Object.freeze(SkRuleComment.DEFAULT.initSkMeta(SKMETALIB.getMetaNode('!')));
		SkRuleChoice.ANY.initCard('*').initSubRules([
			new SkRuleElt().init(SkMatcherAnyElt.SINGLETON, '*', SkRuleChoice.ANY).initSkMeta(SKMETALIB.getMetaNode('*')).linkRules(null),
			new SkRuleAttr().init(SkMatcherAnyAttr.SINGLETON, false).initSkMeta(SKMETALIB.getMetaNode('@')),
			SkRuleText.DEFAULT
		]).freezeRule();
	}
}

export function getGenericSchema(): Schema {
	if (!genericSchema) genericSchema = new Schema(new SkRuleDoc().init(SkRuleChoice.ANY.subRules[0]));
	return genericSchema;
}

let genericSchema: Schema;